<template>
  <div class="v-news-item" @click="goNewsDetails(data.ID)">
    <span class="flag" v-show="flag">哨站</span>
    <h3 class="title">
      {{ data.Title }}
    </h3>
    <span class="date" v-show="!flag">{{ data.Time }}</span>
  </div>
</template>

<script>
export default {
  name: 'v-news-item',
  props: {
    flag: {
      type: Boolean,
      default: true,
    },
    data: {},
  },
  methods: {
    goNewsDetails(id) {
      this.$router.push('/newsdetails/' + id)
    },
  },
}
</script>

<style lang="scss">
.v-news-item {
  height: 35px;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: transform 0.4s ease;
  &:hover {
    transform: translateX(-6px);
  }
  span {
    display: inline-block;
    font-size: $fs-12;
  }
  .flag {
    width: 35px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    color: $white;
    background-color: #e64f5f;
    border-radius: 4px 0 4px 4px;
  }
  .date {
    width: 76px;
    line-height: 18px;
    text-align: right;
    color: $text-color-scd;
  }
  .title {
    flex: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-left: 3px;
  }
}
</style>
