<template>
  <div class="v-paging">
    <div v-if="amount">共{{ amount }}条</div>
    <div class="paging__size">
      <select v-model="pagesize">
        <option :value="size" v-for="(size, index) of sizes" :key="index">{{ size }}条/页</option>
      </select>
    </div>
    <div class="paging__nav">
      <span class="iconfont icon-jiantou3 icon--arrow" @click="pageGo(-1)"></span>
      <ul class="list no-select">
        <li class="list__item" :class="{ 'list__item--cur': curPageIndex === 1 }" @click="goPage(1)">1</li>
        <li class="list__item" v-show="ctrlIndex > 1" @click="mutationOne(-1)">···</li>
        <li
          class="list__item"
          :class="{ 'list__item--cur': curPageIndex === item }"
          v-for="item of pageCountArr"
          :key="item"
          @click="goPage(item)"
        >
          {{ item }}
        </li>
        <li class="list__item" v-show="ctrlIndex < stopIndex" @click="mutationOne(1)">···</li>
        <li
          class="list__item"
          :class="{ 'list__item--cur': curPageIndex === pageCount }"
          v-show="pageCount > 1"
          @click="goPage(pageCount)"
        >
          {{ pageCount }}
        </li>
      </ul>
      <span class="iconfont icon-jiantou2 icon--arrow" @click="pageGo(1)"></span>
    </div>
    <div class="paging__through">
      <span>前往</span>
      <input type="text" v-model="editPageIndex" @blur="goPageByEditIndex" />
      <span>页</span>
    </div>
    <div class="paging__count">共{{ pageCount }}页</div>
  </div>
</template>

<script>
export default {
  name: 'v-paging',
  props: {
    amount: Number,
    sizes: {
      type: Array,
      default: () => {
        return [10]
      },
    },
    maxCount: {
      // >= 2
      type: Number,
      default: 9,
    },
  },
  data() {
    return {
      pagesize: 0,
      pageCount: 0,
      pageCountAllArr: [],
      pageCountArr: [],
      ctrlIndex: 1,
      stopIndex: 1,
      curPageIndex: 1,
      editPageIndex: '',
      showPage: 8,
    }
  },
  mounted() {
    console.log('v-paging-mounted')
    console.log('amount:', this.amount)
    // setTimeout(() => {
    // this.initPaging()
    // }, 200)
  },
  watch: {
    amount() {
      this.initPaging()
    },
  },
  methods: {
    initPaging() {
      // 每页显示多少条数据（初始化：默认采用传过来的sizes数组第一条）
      this.pagesize = this.sizes[0]
      // 总共有多少页
      this.pageCount = Math.ceil(this.amount / this.pagesize)
      // 最多展示多少页
      this.showPage = this.maxCount - 1

      this.pageCountAllArr = this.generatePageCountArr(this.pageCount - 1)
      this.pageCountArr = this.pageCountAllArr.slice(0, this.showPage)
      this.stopIndex = Math.ceil(this.pageCountAllArr.length / this.showPage)
    },
    // 输入页数跳转至某页
    goPageByEditIndex() {
      const tempEditPageIndex = +this.editPageIndex
      if (tempEditPageIndex <= 0 || tempEditPageIndex > this.pageCount) {
        return
      } else if (tempEditPageIndex <= this.showPage) {
        this.ctrlIndex = 1
      } else if (tempEditPageIndex === this.pageCount) {
        this.ctrlIndex = this.stopIndex
      } else {
        this.ctrlIndex = Math.ceil((tempEditPageIndex - 1) / this.showPage)
      }
      this.curPageIndex = tempEditPageIndex
      this.$emit('change-page', this.curPageIndex) // emit
      this.getPageCountArr(this.ctrlIndex)
      this.editPageIndex = ''
    },
    // 点击页数跳转
    goPage(pageIndex) {
      this.curPageIndex = pageIndex
      this.$emit('change-page', this.curPageIndex) // emit
      if (pageIndex === 1) {
        this.ctrlIndex = 1
      } else if (pageIndex === this.pageCount) {
        this.ctrlIndex = this.stopIndex
      }
      this.getPageCountArr(this.ctrlIndex)
    },
    // 点击上一页/下一页按钮跳转
    pageGo(order) {
      const curPageIndex = this.curPageIndex

      // 获取临界点--法一：
      // const criticalArr = this.pageCountAllArr.slice(8 * (this.ctrlIndex - 1), 8 * this.ctrlIndex)
      // const criticalPointForword = criticalArr[criticalArr.length - 1]
      // const criticalPointBack = criticalArr[0]
      // 获取临界点--法二：
      const criticalPointForword = this.ctrlIndex * this.showPage + 1
      const criticalPointBack = this.ctrlIndex * this.showPage - (this.showPage - 2)
      if (order > 0) {
        if (curPageIndex >= this.pageCount) return
        if (curPageIndex >= criticalPointForword) {
          this.mutationOne(1)
        }
      }
      if (order < 0) {
        if (curPageIndex <= 1) return
        if (curPageIndex <= criticalPointBack) {
          this.mutationOne(-1)
        }
      }
      this.curPageIndex = curPageIndex + order
      this.$emit('change-page', this.curPageIndex) // emit
    },
    getPageCountArr(i) {
      this.pageCountArr = this.pageCountAllArr.slice(this.showPage * (i - 1), this.showPage * i)
    },
    mutationOne(order) {
      let ctrlIndex = this.ctrlIndex
      if (order > 0 && ctrlIndex < this.stopIndex) {
        this.ctrlIndex = ++ctrlIndex
      }
      if (order < 0 && ctrlIndex > 1) {
        this.ctrlIndex = --ctrlIndex
      }
      this.getPageCountArr(this.ctrlIndex)
    },
    generatePageCountArr(pageCount) {
      const pageCountAllArr = []
      for (let i = 2; i <= pageCount; i++) {
        pageCountAllArr.push(i)
      }
      return pageCountAllArr
    },
  },
}
</script>

<style lang="scss">
.v-paging {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  color: $text-color-scd;
  select,
  input {
    height: 25px;
    padding: 0;
    border: 1px solid $text-color-scd;
    border-radius: 3px;
    box-sizing: border-box;
  }
  .paging__size {
    margin-left: 15px;
    select {
      cursor: pointer;
    }
  }
  .paging__nav {
    display: flex;
    align-items: center;
    padding: 0 20px;
    .list {
      display: flex;
      align-items: center;
      padding: 0 10px;
    }
    .list__item {
      padding: 5px 8px;
      cursor: pointer;
      &--cur {
        color: #de1e30;
      }
    }
    .icon--arrow {
      font-size: $fs-12;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .paging__through {
    input {
      width: 45px;
      margin: 0 5px;
      text-align: center;
    }
  }
  .paging__count {
    margin: -3px 0 0 15px;
    vertical-align: middle;
  }
}
</style>
