<template>
  <div class="v-message" :class="{ 'v-message-show': isShow }" :style="[boxStyles]">
    <i class="iconfont" :class="iconF"></i>
    <span class="tip-message">{{ title }}</span>
  </div>
</template>
<script>
export default {
  name: 'v-message',
  props: {
    // message框类型
    mType: {
      type: String,
      default: 'info',
    },
    // 提示内容
    title: {
      type: String,
      required: true,
    },
    // 可传入自定义icon图标
    icon: String,
    isShow: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      boxStyles: {
        backgroundColor: '',
        color: '',
        borderColor: '',
      },
      iconF: '',
    }
  },
  mounted() {
    this.initBoxStyles()
  },
  methods: {
    initBoxStyles() {
      switch (this.mType) {
        case 'info':
          this.setBoxStyles('#f4f4f5', '#909399', '#d3d4d6', 'icon-jinggao')
          break
        case 'success':
          this.setBoxStyles('#f0f9eb', '#67c23a', '#c2e7b0', 'icon-zhengque')
          break
        case 'warn':
          this.setBoxStyles('#fdf6ec', '#e6a23c', '#f5dab1', 'icon-jinggao')
          break
        case 'error':
          this.setBoxStyles('#fef0f0', '#f56c6c', '#fbc4c4', 'icon-guanbi')
          break
      }
    },
    setBoxStyles(backgroundColor, color, borderColor, icon) {
      this.boxStyles = {
        backgroundColor,
        color,
        borderColor,
      }
      this.iconF = this.icon || icon
    },
  },
  watch: {
    isShow(newV) {
      if (newV) {
        setTimeout(() => {
          this.$emit('close', false)
        }, 3000)
      }
    },
  },
}
</script>

<style lang="scss">
.v-message {
  min-width: 150px;
  height: 44px;
  line-height: 44px;
  padding: 0 20px;
  background-color: $success-color;
  position: fixed;
  left: 50%;
  top: -8%;
  transform: translateX(-50%);
  z-index: 99999;
  opacity: 0;
  transition: all 0.5s ease;
  border-radius: 5px;
  color: $text-color-scd;
  i {
    vertical-align: middle;
  }
  .tip-message {
    margin-left: 4px;
  }
  &-show {
    top: 8%;
    opacity: 1;
  }
}
</style>
