<template>
  <div class="team">
    <v-header></v-header>
    <main class="w-1200">
      <v-bread-nav :breads="breads"></v-bread-nav>
      <div class="container mt-15">
        <div class="main-area">
          <div class="desc-box">
            <v-desc :zhName="zhName" :enName="enName" :status="status" :imgUrl="imgUrl"></v-desc>
          </div>
          <div class="info-box mt-15 br-rd--3">
            <div class="row">
              <div><span>所属联赛：</span><i></i></div>
              <div>
                <span>成立时间：</span><i>{{ createDate }}</i>
              </div>
              <div>
                <span>球场：</span><i>{{ court }}</i>
              </div>
            </div>
            <div class="row">
              <div><span>主教练：</span><i>佩特科维奇</i></div>
            </div>
          </div>
          <div class="databank-box mt-15">
            <div class="title-list">
              <div
                class="title no-select"
                v-for="item of dataNavList"
                :key="item.id"
                :class="{ 'title-active': curRoute === item.name }"
                @click="teamSubTabClick(item.name)"
              >
                {{ item.title }}
              </div>
            </div>
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </div>
        </div>
        <div class="secondary-area">
          <v-intro type="球队简介">
            <div class="team-intro">
              {{ intro }}
            </div>
          </v-intro>
          <v-news-dyna class="mt-15" :newsData="newsList"></v-news-dyna>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import VHeader from '@/components/v-header'
import VBreadNav from '@/components/v-bread-nav'
import VDesc from '@/components/v-desc'
import VIntro from '@/components/v-intro'
import VNewsDyna from '@/components/v-news-dyna'
import { getCountryData } from '@/api/home'
export default {
  name: 'Country',
  components: {
    VHeader,
    VBreadNav,
    VDesc,
    VIntro,
    VNewsDyna,
  },
  data() {
    return {
      breads: ['首页', '足球资料库'],
      zhName: '',
      enName: '',
      status: '球队市值：-',
      imgUrl: '',
      intro: '',
      createDate: '',
      court: '',
      coach: '',
      dataNavList: [
        { id: 0, title: '比赛', name: 'comp' },
        // { id: 1, title: '数据', name: 'data' },
        { id: 2, title: '阵容', name: 'player' },
      ],
      curRoute: '',
      newsList: [],
    }
  },
  mounted() {
    this.splitRoute(this.$route.path)
    this.initData()
  },
  methods: {
    async initData() {
      const countryID = this.$route.params.id
      const res = await getCountryData({ CountryID: countryID })
      const { CountryData, AdvisoryList } = res.data.Data
      this.zhName = CountryData[0].Name
      this.enName = CountryData[0].EnglishName
      this.imgUrl = CountryData[0].Img
      this.intro = CountryData[0].Introduction
      this.breads.push(this.zhName)
      this.createDate = CountryData[0].CreationTime.split(' ')[0]
      this.court = CountryData[0].HomeCourt
      this.newsList = AdvisoryList
    },
    teamSubTabClick(path) {
      this.$router.push(`/team/1/${path}`)
    },
    splitRoute(path) {
      let rtArr = path.split('/')
      this.curRoute = rtArr[rtArr.length - 1]
    },
  },
  watch: {
    $route(newRt) {
      this.splitRoute(newRt.path)
    },
  },
}
</script>

<style lang="scss">
.team {
  .container {
    display: flex;
  }
  .main-area {
    flex: 1;
  }
  .secondary-area {
    width: 300px;
    margin-left: 20px;
  }
  .info-box {
    background-color: $white;
    padding: 25px 15px;
    box-shadow: 0 2px 6px $br-color-base;
    .row {
      font-size: $fs-12;
      &:nth-child(2) {
        margin-top: 15px;
      }
      div {
        display: inline-block;
        &:not(:first-child) {
          margin-left: 50px;
        }
        i {
          color: $h-color;
        }
      }
    }
  }
  .databank-box {
    // box-shadow: 0 2px 6px $br-color-base;
    .title-list {
      display: flex;
      background-color: #f0f0f0;
      .title {
        width: 84px;
        height: 44px;
        line-height: 44px;
        text-align: center;
        cursor: pointer;
        &-active {
          border-bottom: 2px solid #e74c5b;
          color: #e74c5b;
        }
      }
    }
  }
  .team-intro {
    padding: 10px;
    font-size: $fs-12;
    line-height: 18px;
    letter-spacing: 1px;
    color: $h-color;
  }
}
</style>
