<template>
  <div class="v-team-list">
    <div class="mt-15" v-for="(item, i) of teamList" :key="item.CountryID">
      <div class="simplify-box">
        <div class="group">{{ item.Group }}</div>
        <div class="country-list">
          <div class="country" v-for="item2 of item.Country" :key="item2.ID" @click="goCountry(item2.ID)">
            <div class="img">
              <img :src="item2.Img" :alt="item2.Name" />
            </div>
            <div class="text">
              <h3>{{ item2.Name }}</h3>
              <span>排名：{{ item2.WorldRanking }}</span>
            </div>
          </div>
        </div>
        <div class="expand-btn" @click="expandBoxChange(i)">
          <span class="iconfont icon-jiantou" v-show="teamItemIndex === i"></span>
          <span class="iconfont icon-jiantou1" v-show="teamItemIndex !== i"></span>
        </div>
      </div>
      <div class="expand-box" v-if="teamItemIndex === i">
        <div class="expand-item" v-for="(expTeam, j) of expandList" :key="j">
          <div class="left-box">
            <div class="country">
              <router-link :to="'/team/' + expTeam.CountryID" class="router-link--reset">
                <img :src="expTeam.Country[0].Img" :alt="expTeam.Country[0].Name" />
                <h3>{{ expTeam.Country[0].Name }}</h3>
                <span>排名:{{ expTeam.Country[0].WorldRanking }}</span>
              </router-link>
            </div>
            <div class="player">
              <div class="play-item" v-for="player of expTeam.Player" :key="player.ID">
                <div @mouseover="playerIndex = player.ID" @mouseout="playerIndex = 0">
                  <router-link :to="'/player/' + player.ID">
                    <img :src="player.Img" :alt="player.Name" />
                  </router-link>
                </div>
                <div
                  class="tipBox"
                  v-show="playerIndex === player.ID"
                  @mouseover="playerIndex = player.ID"
                  @mouseout="playerIndex = 0"
                >
                  <div class="head">
                    <img :src="player.Img" :alt="player.Name" />
                    <h3>{{ player.Name }}</h3>
                    <span>{{ player.EnglishName }}</span>
                  </div>
                  <div class="body">
                    <div class="item">
                      <div>
                        <span>身价：</span>
                        <p>{{ player.Worth }}万欧</p>
                      </div>
                      <div>
                        <span>位置：</span>
                        <p>{{ player.Position }}</p>
                      </div>
                    </div>
                    <div class="item">
                      <div>
                        <span>出生日期：</span>
                        <p>{{ player.DateBirth }}</p>
                      </div>
                      <div>
                        <span>身高：</span>
                        <p>{{ player.Height }}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="more-player">
              <router-link :to="'/team/' + expTeam.CountryID" class="router-link--reset"
                ><span class="iconfont icon-jiantou2"></span
              ></router-link>
            </div>
          </div>
          <div class="right-box">
            <div class="title">
              <span>近10场数据</span>
            </div>
            <div class="pie-wrap">
              <v-pie :pid="expTeam.CountryID"></v-pie>
              <p class="desc">
                {{ expTeam.Country[0].Level | numToCN }}平{{ expTeam.Country[0].Win | numToCN }}胜{{
                  expTeam.Country[0].Lose | numToCN
                }}负
              </p>
            </div>
            <div class="score-data">
              <ul class="data-list">
                <li>
                  <span>{{ expTeam.Country[0].Goal }}</span>
                  <span>进球</span>
                </li>
                <li>
                  <span>{{ expTeam.Country[0].LostBall }}</span>
                  <span>失球</span>
                </li>
                <li>
                  <span>{{ expTeam.Country[0].WinRate }}</span>
                  <span>胜率</span>
                </li>
                <li>
                  <span>{{ expTeam.Country[0].WinnerRate }}</span>
                  <span>赢率</span>
                </li>
                <li>
                  <span>{{ expTeam.Country[0].BigRate }}</span>
                  <span>大率</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VPie from '@/components/v-pie'
import { getPlayerList } from '@/api/home'
import { numToChinese } from '@/utils/tools'
export default {
  name: 'v-team-list',
  components: {
    VPie,
  },
  data() {
    return {
      teamItemIndex: -1,
      currentTeamItem: -2,
      teamList: [],
      expandAllList: [],
      expandList: [],
      playerIndex: 0,
    }
  },
  filters: {
    numToCN(val) {
      return numToChinese(val)
    },
  },
  mounted() {
    this.initTeamBox()
  },
  methods: {
    goCountry(id) {
      this.$router.push('/team/' + id)
    },
    async initTeamBox() {
      const res = await getPlayerList()
      this.teamList = res.data.Data.CountryData
      this.expandAllList = res.data.Data.List
    },
    expandBoxChange(i) {
      // this.teamItemIndex = this.currentTeamItem !== i ? i : -2
      // this.currentTeamItem = this.currentTeamItem !== i ? i : -2
      if (this.currentTeamItem !== i) {
        this.teamItemIndex = i
        this.currentTeamItem = i
        this.expandList = this.expandAllList.filter(item => item.Group2 === i)[0].Data
      } else {
        this.teamItemIndex = -2
        this.currentTeamItem = -2
      }
    },
  },
}
</script>

<style lang="scss">
.v-team-list {
  min-height: 700px;
  background-color: $white;
  padding: 5px 20px 20px;
  // overflow: hidden;
  .simplify-box {
    height: 100px;
    display: flex;
    align-items: center;
    color: $text-color;
    background: url(~@/assets/images/pic/bg-3.png) no-repeat left top;
    background-size: 100% 100%;
    .group {
      width: 15%;
      padding-left: 40px;
      font-size: $fs-18;
    }
    .country-list {
      display: flex;
      width: 78%;
    }
    .country {
      width: 25%;
      display: flex;
      align-items: center;
      cursor: pointer;
      img {
        width: 60px;
        height: 60px;
        overflow: hidden;
        border-radius: 50%;
        border: 1px solid $br-color-base;
        vertical-align: middle;
      }
      .text {
        margin-left: 5px;
        h3 {
          font-size: $fs-16;
          font-weight: 600;
        }
        span {
          color: $text-color-scd;
          display: inline-block;
          margin-top: 10px;
        }
      }
    }
    .expand-btn {
      width: 30px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      &:hover {
        background-color: $white;
      }
    }
  }
  .expand-box {
    padding: 20px 0;
    box-sizing: border-box;
    .expand-item {
      display: flex;
    }
    .left-box {
      width: 46%;
      display: flex;
      align-items: center;
      border-right: 1px solid $br-color-base;
      padding-right: 20px;
      box-sizing: border-box;
    }
    .country {
      width: 150px;
      text-align: center;
      img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
      }
      h3 {
        font-size: $fs-16;
        margin-top: 10px;
      }
      span {
        color: $text-color-scd;
        margin-top: 5px;
        display: block;
      }
    }
    .player {
      height: 108px;
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-content: space-between;
      .play-item {
        position: relative;
        width: 20%;
        cursor: pointer;
        img {
          width: 46px;
          height: 46px;
          border-radius: 50%;
          overflow: hidden;
          border: 1px solid $br-color-base;
        }
      }
      .tipBox {
        position: absolute;
        left: 0;
        top: -12px;
        width: 180px;
        color: $white;
        padding: 15px 0;
        background-color: $primary-color;
        transform: translate(-37%, -100%);
        border-radius: 3px;
        &::after {
          content: '';
          display: block;
          position: absolute;
          left: 50%;
          bottom: -16px;
          border: 8px solid;
          border-color: $primary-color transparent transparent transparent;
          transform: translateX(-50%);
        }
        span {
          color: #eee;
          font-size: $fs-12;
        }
        .head {
          text-align: center;
          h3 {
            padding: 8px 0 3px;
          }
        }
        .body {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          margin-top: 20px;
          .item {
            width: 100%;
            display: flex;
            &:nth-child(2) {
              margin-top: 10px;
            }
            div {
              &:nth-child(1) {
                width: 45%;
                margin-left: 13%;
              }
              &:nth-child(2) {
                width: 25%;
                margin-left: 8%;
              }
            }
            p {
              padding: 10px 0;
            }
          }
        }
      }
    }
    .more-player {
      width: 20px;
      text-align: center;
      height: 20px;
      line-height: 20px;
      background-color: #f7f7f8;
      border-radius: 3px;
      cursor: pointer;
      .icon-jiantou2 {
        vertical-align: middle;
        font-size: $fs-12;
      }
    }
    .right-box {
      width: 54%;
      display: flex;
      align-items: center;
      .title {
        width: 20%;
        color: $h-color;
        font-size: $fs-16;
        text-align: center;
      }
      .pie-wrap {
        width: 26%;
        height: 100%;
        position: relative;
        .desc {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          font-size: $fs-12;
        }
      }
      .score-data {
        width: 54%;
        height: 100%;
        .data-list {
          display: flex;
          flex-wrap: wrap;
          li {
            width: 98px;
            display: flex;
            flex-direction: column;
            margin-bottom: 15px;
            &:not(:nth-child(3n + 1)) {
              margin-left: 15px;
            }
            span {
              height: 24px;
              line-height: 24px;
              text-align: center;
            }
            span:nth-child(1) {
              background-color: $primary-color;
              color: $white;
            }
            span:nth-child(2) {
              border: 1px solid $primary-color;
              color: $text-color-scd;
            }
          }
        }
      }
    }
  }
}
</style>
