<template>
  <div class="team-player">
    <div class="play-container">
      <table>
        <thead>
          <tr>
            <th>序号</th>
            <th>球员</th>
            <th>出生日</th>
            <th>身高(cm)</th>
            <th>体重(kg)</th>
            <th>位置</th>
            <th>国籍</th>
            <th>进球</th>
            <th>助攻</th>
            <th>红牌</th>
            <th>黄牌</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(player, index) of playerList" :key="index">
            <td>{{ index + 1 }}</td>
            <td class="col-player" @click="goPlayer(player.ID)">
              <img :src="player.Img" alt="" /><span>{{ player.Name }}</span>
            </td>
            <td>{{ player.DateBirth.split(' ')[0] }}</td>
            <td>{{ player.BodyWeight }}</td>
            <td>{{ player.Height }}</td>
            <td>{{ player.Position }}</td>
            <td>{{ player.CountryName }}</td>
            <td>0</td>
            <td>0</td>
            <td>0</td>
            <td>0</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { getCountryPlayers } from '@/api/home'
export default {
  name: 'TeamPlayer',
  data() {
    return {
      playerList: [],
    }
  },
  created() {
    console.log('TeamPlayer-created')
    this.initPlayers()
  },
  methods: {
    goPlayer(id) {
      this.$router.push('/player/' + id)
    },
    async initPlayers() {
      const countryID = this.$route.params.id
      const res = await getCountryPlayers({ CountryID: countryID })
      this.playerList = res.data.Data.PlayerList
    },
  },
}
</script>

<style lang="scss">
.team-player {
  background-color: $white;
  .play-container {
    padding: 15px;
    table {
      width: 100%;
      font-size: $fs-12;
    }
    th {
      height: 45px;
      vertical-align: middle;
      border: 1px solid $br-color-base;
      position: relative;
      &::before {
        content: '';
        width: 100%;
        height: 8px;
        background-color: #717390;
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(-100%);
      }
    }
    tr:hover {
      background-color: #eeeeee;
    }
    td {
      height: 40px;
      text-align: center;
      vertical-align: middle;
      border: 1px solid $br-color-base;
      &:nth-child(2) {
        cursor: pointer;
      }
    }
    .col-player {
      width: 220px;
      text-align: left;
      padding-left: 40px;
      box-sizing: border-box;
      img {
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin-right: 3px;
        border-radius: 50%;
      }
    }
  }
}
</style>
