<template>
  <div class="event-guide">
    <v-header :navIndex="5"></v-header>
    <v-banner></v-banner>
    <main class="w-1200 main-strange">
      <div class="title-box">
        <p class="title-text">2020欧洲杯介绍</p>
        <p class="content-text">
          欧洲杯是由欧足联承办每四年举行一次的足坛盛会，2020年欧洲杯原定于2020年6月12日至2020年7月12日在欧足联旗下12个成员国的12座城市进行。2020年3月17日，欧足联宣布由于新冠疫情影响，欧洲杯推迟一年进行。2020年6月17日，欧足联正式确认2020年欧洲杯将在2021年6月12日至2021年7月12日举行，比赛名称仍保留“2020年欧洲杯”（UEFA
          Euro
          2020）。为纪念欧洲杯60年，本届欧洲杯将采用无主办国的巡回赛方式在欧洲的12个城市举行，半决赛和决赛将在英国伦敦的温布利球场举行。
        </p>
      </div>

      <div class="title-box">
        <p class="title-text">赛制介绍</p>
        <p class="content-text">
          2020年欧洲杯共有24支球队参加正赛,分为6个小组，以小组单循环得分制比赛，每组得分最高两队与4个最好小组第三晋身16强淘汰赛，由十六强至决赛均采取一场决胜负的形式进行，若90分钟内打成平手，需加时赛再决胜负，加时并不设黄金入球或银球制，两队必须完成加时上、下半场各15分钟的赛事，加时入球较多的一队胜出，若加时后打成平手则需以点球分胜负，1984年起，欧洲杯已取消季军赛，按积分与净胜球确定三、四名。
        </p>
      </div>

      <div class="teams-box">
        <p class="teams-title">参赛球队</p>
        <ul class="tab-box">
          <li class="tab-item" @click="active(1)" :class="{ active_li_1: index == 1 }">
            <p :class="{ active_p_1: index == 1 }">A组</p>
          </li>
          <li class="tab-item" @click="active(2)" :class="{ active_li: index == 2 }">
            <p :class="{ active_p: index == 2 }">B组</p>
          </li>
          <li class="tab-item" @click="active(3)" :class="{ active_li: index == 3 }">
            <p :class="{ active_p: index == 3 }">C组</p>
          </li>
          <li class="tab-item" @click="active(4)" :class="{ active_li: index == 4 }">
            <p :class="{ active_p: index == 4 }">D组</p>
          </li>
          <li class="tab-item" @click="active(5)" :class="{ active_li: index == 5 }">
            <p :class="{ active_p: index == 5 }">E组</p>
          </li>
          <li class="tab-item" @click="active(6)" :class="{ active_li_6: index == 6 }">
            <p :class="{ active_p_1: index == 6 }">F组</p>
          </li>
        </ul>
      </div>

      <div class="teams-content">
        <div class="teams-content-item" v-for="(item, index) of contents" :key="index">
          <p class="item-left">
            <img :src="item.Img" />
            <span>{{ item.Name }}</span>
          </p>
          <p class="item-right">
            {{ item.Introduction }}
          </p>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { postEventGuide } from '@/api/news.js'
import VBanner from '@/components/v-banner.vue'
import VHeader from '../components/v-header.vue'
export default {
  name: 'EventGuide',
  components: {
    VBanner,
    VHeader,
  },
  data() {
    return {
      index: 1, //tab切换变量
      contents: null,
      list: null,
    }
  },
  async created() {
    const res = await postEventGuide()
    this.list = res.data.Data
    this.contents = this.list.CountryListA
  },
  mounted() {
    this.contents = [1] //默认值
  },
  methods: {
    active(index) {
      //tab切换方法
      this.index = index
      //根据下标向contents赋值
      switch (index) {
        case 1:
          this.contents = this.list.CountryListA
          break
        case 2:
          this.contents = this.list.CountryListB
          break
        case 3:
          this.contents = this.list.CountryListC
          break
        case 4:
          this.contents = this.list.CountryListD
          break
        case 5:
          this.contents = this.list.CountryListE
          break
        case 6:
          this.contents = this.list.CountryListF
          break
      }
    },
  },
}
</script>

<style lang="scss">
.event-guide {
  //---------------------------------
  @mixin flex-ceneter {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .main-strange {
    background: linear-gradient(135deg, transparent 0px, $white 0) top left,
      linear-gradient(-135deg, transparent 15px, $white 0) top right, linear-gradient(-45deg, transparent 0px, $white 0) bottom right,
      linear-gradient(45deg, transparent 15px, $white 0) bottom left;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    box-sizing: border-box;
    padding: 12px 20px 20px;
  }

  .active_li_1 {
    border-top: 36px solid $primary-color !important;
  }
  .active_p_1 {
    background: $primary-color !important;
    color: $white !important;
  }

  .active_li_6 {
    border-bottom: 36px solid $primary-color !important;
  }

  .active_li {
    background: $primary-color !important;
  }

  .active_p {
    color: $white !important;
  }
  //-----------------------------------------------------

  .title-box {
    border-bottom: 1px solid $br-color-base;
  }

  .teams-title,
  .title-text {
    height: 65px;
    font-size: $fs-18;
    color: $black;
    font-weight: 600;
    line-height: 65px;
    padding-left: 13px;
    position: relative;
    cursor: pointer;
  }
  .teams-title::after,
  .title-text::after {
    content: '';
    position: absolute;
    width: 3px;
    height: 12px;
    background: $primary-color;
    border-radius: 1px;
    left: 0;
    top: 26px;
  }
  .content-text {
    height: 90px;
    background: #f7f7f8;
    line-height: 20px;
    font-weight: 600;
    @include flex-ceneter;
    padding: 15px;
    margin-bottom: 20px;
    box-sizing: border-box;
    color: gray;
  }
  //-------------------------------TAB样式
  .tab-box {
    width: 1160px;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
  }
  .tab-item {
    cursor: pointer;
    transition: all 0.3s ease;
  }
  .tab-item > p {
    transition: all 0.3s ease;
    pointer-events: none;
  }

  .tab-item:nth-child(6),
  .tab-item:nth-child(1) {
    width: 170px;
    position: relative;
  }

  .tab-item:nth-child(1) {
    border-right: 20px solid transparent;
    border-top: 36px solid #f7f7f8;
  }
  .tab-item:nth-child(6) {
    border-left: 20px solid transparent;
    border-bottom: 36px solid #f7f7f8;
  }

  .tab-item:nth-child(1):hover {
    border-top: 36px solid$primary-color;
  }
  .tab-item:nth-child(6):hover {
    border-bottom: 36px solid $primary-color;
  }

  .tab-item:nth-child(6):hover > p,
  .tab-item:nth-child(1):hover > p {
    background: $primary-color;
    color: $white;
  }

  .tab-item:nth-child(6) > p,
  .tab-item:nth-child(1) > p {
    width: 170px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    color: gray;
    background: #f7f7f8;
    position: absolute;
    top: 0;
  }
  .tab-item:nth-child(1) > p {
    top: -36px;
  }

  .tab-item:nth-child(2),
  .tab-item:nth-child(3),
  .tab-item:nth-child(4),
  .tab-item:nth-child(5) {
    width: 170px;
    position: relative;
    background: #f7f7f8;
    transform: rotateZ(0deg) skew(-30deg, 0deg);
  }

  .tab-item:nth-child(2) > p,
  .tab-item:nth-child(3) > p,
  .tab-item:nth-child(4) > p,
  .tab-item:nth-child(5) > p {
    width: 170px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    color: gray;
    transform: translateY(0px);
    transform: rotateZ(0deg) skew(30deg, 0deg);
    position: absolute;
    top: 0;
  }
  .tab-item:nth-child(2):hover,
  .tab-item:nth-child(3):hover,
  .tab-item:nth-child(4):hover,
  .tab-item:nth-child(5):hover {
    background: $primary-color;
  }

  .tab-item:nth-child(2):hover > p,
  .tab-item:nth-child(3):hover > p,
  .tab-item:nth-child(4):hover > p,
  .tab-item:nth-child(5):hover > p {
    color: $white;
  }

  //--------------------------------------
  .teams-content {
    margin-top: 20px;
  }
  .teams-content-item {
    margin: 0 auto;
    width: 1142px;
    height: 90px;
    background: #f7f7f8;
    display: flex;
    margin-bottom: 10px;
  }
  .teams-content-item > p {
    @include flex-ceneter;
    color: gray;
    font-weight: 600;
  }

  .item-left {
    flex: 2;
    font-weight: 600;
  }
  .item-left > img {
    margin: 0 10px;
    width: 40px;
    height: 40px;
    margin-left: -50px;
    border-radius: 50%;
  }
  .item-left > span {
    display: inline-block;
    width: 80px;
    margin-right: -50px;
  }
  .item-right {
    justify-content: flex-start !important;
    flex: 8;
    box-sizing: border-box;
    line-height: 25px;
    padding-right: 15px;
  }
}
</style>
