<template>
  <div class="home">
    <v-header></v-header>
    <v-banner></v-banner>
    <div class="w-1200">
      <div ref="scheduleRef">
        <schedule-carousel></schedule-carousel>
      </div>
      <div class="info-container" ref="infoboxRef">
        <div class="pic-container">
          <pic-box :mainPic="mainPic" :subPicList="subPicList"></pic-box>
        </div>
        <div class="news-box">
          <v-news-item v-for="item of newsList" :key="item.ID" :data="item"></v-news-item>
        </div>
        <div class="player-bill--container">
          <player-bill-box></player-bill-box>
        </div>
      </div>
      <div class="competition-container" ref="competitionRef">
        <div class="title-wrapper"><img src="../assets/images/pic/nav-title.png" alt="赛事数据" /></div>
        <competition-box></competition-box>
      </div>
      <div class="teams-container" ref="teamsRef">
        <div class="title-wrapper"><img src="../assets/images/pic/team-nav.png" alt="参赛球队" /></div>
        <v-team-list></v-team-list>
      </div>
    </div>
    <div class="court-container" ref="courtRef">
      <div class="title-wrapper"><img src="../assets/images/pic/court-nav.png" alt="球场" /></div>
      <div class="w-1200">
        <v-map width="1200" height="700"></v-map>
      </div>
    </div>
    <div class="aside-scroll">
      <div class="scroll-index">
        <div class="bg--logo"></div>
        <div class="btn--bet">
          <a href="http://hqbet1602.com" class="router-link--reset" style="color:#fff" target="_blank">赛事竞猜</a>
        </div>
        <ul class="list--nav">
          <li @click="goScheduleArea('scheduleRef')">热门赛程</li>
          <li @click="goScheduleArea('infoboxRef')">情报快讯</li>
          <li @click="goScheduleArea('competitionRef')">赛事数据</li>
          <li @click="goScheduleArea('teamsRef')">参赛球队</li>
          <li @click="goScheduleArea('courtRef')">球馆探索</li>
        </ul>
        <div class="qr"><img src="@/assets/images/pic/qr-hq-1602.png" alt="扫码下载" /></div>
        <div class="btn--totop" @click="goPageTop"><span class="iconfont icon-topagetop"></span></div>
        <div class="bg--triangle"></div>
      </div>
    </div>
  </div>
</template>

<script>
import VNewsItem from '@/components/v-news-item'
import VHeader from '@/components/v-header'
import VBanner from '@/components/v-banner'
import VMap from '@/components/v-map'
import ScheduleCarousel from '@/components/home/schedule-carousel'
import PicBox from '@/components/home/pic-box'
import PlayerBillBox from '@/components/home/player-bill-box'
import CompetitionBox from '@/components/home/competition-box'
import VTeamList from '@/components/v-team-list'
import 'swiper/css/swiper.css'
import { getNewsData } from '@/api/home'
export default {
  name: 'Home',
  components: {
    VNewsItem,
    VHeader,
    VBanner,
    VMap,
    ScheduleCarousel,
    PicBox,
    PlayerBillBox,
    CompetitionBox,
    VTeamList,
  },
  data() {
    return {
      mainPic: {},
      subPicList: [],
      newsList: [],
      pic: '',
    }
  },
  mounted() {
    this.initNewsPic()
  },
  methods: {
    goPageTop() {
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      })
    },
    goScheduleArea(refName) {
      const refNode = this.$refs[refName]
      refNode.scrollIntoView({ behavior: 'smooth' })
    },
    async initNewsPic() {
      const res = await getNewsData()
      const newsAllList = res.data.Data.AdvisoryList
      this.mainPic = newsAllList[0]
      this.pic = newsAllList[0].Img
      this.subPicList = newsAllList.slice(1, 5)
      this.newsList = newsAllList.slice(4)
    },
  },
}
</script>

<style lang="scss">
.home {
  margin: 0 auto;
  .title-wrapper {
    text-align: center;
    padding: 35px 0;
  }
  .info-container {
    display: flex;
    margin-top: 20px;
    .pic-container {
      width: 42%;
    }
    .player-bill--container {
      width: 25%;
      margin-left: 10px;
    }
    .news-box {
      width: 33%;
      background-color: $white;
      margin-left: 10px;
      padding: 0 10px;
      box-sizing: border-box;
    }
  }
  .competition-container {
    margin-top: 20px;
  }
  .teams-container {
    margin-top: 20px;
  }
  .court-container {
    margin-top: 60px;
    background: url(~@/assets/images/pic/place.png) no-repeat left top;
    background-size: 100% 100%;
    overflow: hidden;
    padding-top: 20px;
  }
  .aside-scroll {
    position: fixed;
    right: 20px;
    bottom: 18%;
    z-index: 9999;
  }
  .scroll-index {
    position: relative;
    width: 96px;
    background-color: $primary-color;
    color: $white;
    padding: 5px 10px;
    box-sizing: border-box;
    @keyframes scale {
      to {
        transform: scale3d(1.4, 1.4, 1);
      }
    }
    .bg--logo {
      position: absolute;
      left: -16px;
      top: -80px;
      width: 126px;
      height: 80px;
      background: url(~@/assets/images/pic/yuyan.png) no-repeat left center;
      background-size: 100% 100%;
    }
    .btn--bet {
      height: 26px;
      line-height: 26px;
      text-align: center;
      font-family: '幼圆';
      font-size: $fs-12;
      background-color: #ff5252;
      border-radius: 13px;
      cursor: pointer;
      animation: scale 0.6s infinite alternate;
    }
    .list--nav {
      margin-top: 10px;
      li {
        height: 36px;
        line-height: 36px;
        text-align: center;
        border-bottom: 1px solid #78d0de;
        cursor: pointer;
        &:hover {
          background-color: rgba(255, 255, 255, 0.4);
        }
      }
    }
    .qr {
      img {
        width: 100%;
      }
    }
    .btn--totop {
      color: $white;
      text-align: center;
      margin-top: 15px;
      cursor: pointer;
      .icon-topagetop {
        font-size: $fs-28;
      }
    }
    .bg--triangle {
      position: absolute;
      left: 0;
      bottom: -30px;
      width: 96px;
      height: 30px;
      background: url(~@/assets/images/pic/triangle.png) no-repeat left center;
      background-size: cover;
    }
  }
}
</style>
