<template>
  <div class="v-radar">
    <div id="combat" style="width: 410px;height: 220px;"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts/core'
import { TooltipComponent } from 'echarts/components'
import { RadarChart } from 'echarts/charts'
import { CanvasRenderer } from 'echarts/renderers'
echarts.use([TooltipComponent, RadarChart, CanvasRenderer])
var radarChart = null
export default {
  name: 'v-radar',
  mounted() {
    this.initCombatEchart()
  },
  beforeDestroy() {
    echarts.dispose(radarChart)
  },
  methods: {
    initCombatEchart() {
      var chartDom = document.getElementById('combat')
      radarChart = echarts.init(chartDom)
      var option = {
        tooltip: {},
        radar: {
          // shape: 'circle',
          axisName: {
            textStyle: {
              color: 'rgba(0, 0, 0, 0.4)',
            },
          },
          indicator: [
            { name: '进攻', max: 100 },
            { name: '创造力', max: 100 },
            { name: '战术', max: 100 },
            { name: '防守', max: 100 },
            { name: '技术', max: 100 },
          ],
        },
        series: [
          {
            name: '球员分析',
            type: 'radar',
            data: [
              {
                value: [97, 67, 73, 32, 84],
                name: '球员实战数据',
              },
            ],
            areaStyle: {
              color: '#de1e30',
              opacity: 0.2,
            },
            itemStyle: {
              color: '#fff',
              borderColor: '#de1e30',
              borderWidth: 0.4,
            },
            symbol: 'circle', //拐点格式样式 如:实心圆...
            symbolSize: 5, //拐点的大小
            lineStyle: {
              color: '#de1e30',
              type: 'solid',
              width: 2,
            },
          },
        ],
      }
      option && radarChart.setOption(option)
    },
  },
}
</script>

<style></style>
