<template>
  <div class="watch">
    <v-header></v-header>
    <div class="desc-container">
      <div class="team-aside">
        <span>{{ hotSchedule.AName }}</span>
        <img :src="hotSchedule.AImg1" alt="" />
        <!-- <span class="score">-</span> -->
      </div>
      <div class="team-center">
        <p class="time">{{ hotSchedule.GroupValue }} {{ hotSchedule.Remarks }} {{ hotSchedule.GameDateTime }}</p>
        <p class="vs">VS</p>
      </div>
      <div class="team-aside">
        <!-- <span class="score">-</span> -->
        <img :src="hotSchedule.BImg1" alt="" />
        <span>{{ hotSchedule.BName }}</span>
      </div>
    </div>
    <div class="video-container">
      <div class="video-box">
        <v-video-room :url="liveUrl"></v-video-room>
      </div>
    </div>
  </div>
</template>

<script>
import VHeader from '@/components/v-header'
import VVideoRoom from '@/components/v-video-room'
export default {
  name: 'Watch',
  components: {
    VHeader,
    VVideoRoom,
  },
  data() {
    return {
      liveUrl: '',
      hotSchedule: {},
    }
  },
  created() {
    const watchID = this.$route.params.id
    const hotSchedule = this.$store.state.hotSchedules.filter(item => item.ID == watchID)[0]
    this.hotSchedule = hotSchedule
    // this.liveUrl = 'http://vjs.zencdn.net/v/oceans.mp4'
    this.liveUrl = hotSchedule.LiveUrl || ''
  },
}
</script>

<style lang="scss">
.watch {
  .desc-container {
    display: flex;
    justify-content: center;
    background-color: $white;
    padding: 24px;
    border-bottom: 1px solid $br-color-base;
    img {
      width: 54px;
      height: 54px;
      vertical-align: middle;
      margin: 0 20px;
    }
    span {
      vertical-align: middle;
    }
    .team-aside {
      font-size: $fs-18;
      .score {
        font-size: 40px;
        color: $h-color;
      }
    }
    .team-center {
      margin: 0 60px;
      text-align: center;
      .time {
        font-size: $fs-12;
        color: $text-color-scd;
      }
      .vs {
        margin-top: 10px;
        font-size: $fs-18;
      }
    }
  }
  .video-container {
    margin-top: 20px;
    padding-bottom: 20px;
    background: url(~@/assets/images/pic/watch-bg.png) no-repeat left top;
    background-size: 100% 100%;
    .video-box {
      width: 1200px;
      margin: 0 auto;
    }
  }
}
</style>
