<template>
  <div class="news">
    <v-header :navIndex="2"></v-header>
    <v-banner></v-banner>
    <main class="w-1200 main-strange">
      <div class="news-item" v-for="item in list" :key="item.ID" @click="gotoNews(item.ID)">
        <div class="item-left">
          <img :src="item.Img" @load="ImgLoad($event)" />
        </div>
        <div class="item-right">
          <p>{{ item.Title }}</p>
          <p>
            {{ item.Psreview }}
          </p>
          <p>
            <span>{{ item.Time }}</span>
            <span class="iconfont">&#xe683;</span>
          </p>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { getNewsList } from '@/api/news.js'
import VHeader from '@/components/v-header'
import VBanner from '@/components/v-banner'
export default {
  name: 'News',
  components: {
    VHeader,
    VBanner,
  },
  data() {
    return {
      list: null, //文章数据列表
      page: 1, //文章请求参数
      timeOut: null, //节流事件ID
    }
  },
  beforeDestroy() {
    //页面销毁,移除全局滚动事件
    window.removeEventListener('scroll', this.debounce)
  },
  async created() {
    //获取文章数据
    const res = await getNewsList({ page: this.page })
    this.list = res.data.Data.AdvisoryList
  },
  mounted() {
    // 滚动事件
    window.addEventListener('scroll', this.debounce)
  },
  watch: {
    $route(newVal, preVal) {
      if (newVal.path === '/news') {
        console.log('add news scroll listener')
        window.addEventListener('scroll', this.debounce)
      }
      if (preVal.path === '/news') {
        console.log('remove news scroll listener')
        window.removeEventListener('scroll', this.debounce)
      }
    },
  },
  methods: {
    //文章页跳转处理函数
    gotoNews(id) {
      sessionStorage.removeItem('text')
      this.$router.push('/newsdetails/' + id)
    },
    //img加载过渡响应函数
    ImgLoad(event) {
      event.target.style.opacity = '1'
    },
    //滚动事件响应函数
    debounce() {
      if (this.timeOut !== null) {
        clearTimeout(this.timeOut)
      }

      this.timeOut = setTimeout(async () => {
        //最大滚动加载数量100
        if (this.list.length && this.list.length >= 100) {
          return 0
        }
        let topAll = document.documentElement.scrollHeight
        let scroll = document.documentElement.clientHeight + document.documentElement.scrollTop
        let value = topAll - scroll
        let addList = null
        //滚动距离底部小于100
        if (value < 100) {
          this.page++
          addList = await getNewsList({ page: this.page })
          addList = addList.data.Data.AdvisoryList
          // sessionStorage.setItem('page', this.page) //保存当前页数
          console.log('小于200,发送AJAX', addList)
        }
        //滚动距离底部小于20
        if (value < 20) {
          this.list = [...this.list, ...addList]
        }
      }, 100)
    },
  },
}
</script>

<style lang="scss">
@mixin flex-ceneter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.news {
  .main-strange {
    min-height: 800px;
    padding: 5px 20px;
    position: relative;
    background: linear-gradient(135deg, transparent 0px, $white 0) top left,
      linear-gradient(-135deg, transparent 15px, $white 0) top right, linear-gradient(-45deg, transparent 0px, $white 0) bottom right,
      linear-gradient(45deg, transparent 15px, $white 0) bottom left;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    box-sizing: border-box;
  }

  .news-item {
    width: 100%;
    height: 165px;
    border-bottom: 1px solid $br-color-base;
    display: flex;
    @include flex-ceneter;
    cursor: pointer;
  }

  .news-item:last-child {
    border-bottom: none;
  }
  //-------------------------------------
  .item-left {
    flex: 2;
    overflow: hidden;
    background-image: url('../assets/images/pic/loading.gif');
    background-size: 15% 30%;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
  }
  .item-left:hover > img {
    transform: scale(1.1);
  }
  .item-left > img {
    width: 240px;
    height: 130px;
    opacity: 0;
    transition: transform 0.7s ease-in-out, opacity 1.1s ease-in-out;
  }
  //--------------------------------------
  .item-right {
    flex: 7;
    height: 130px;
    padding: 0 15px;
    box-sizing: border-box;
    color: gray;
  }
  .item-right:hover > p:nth-child(1) {
    color: $primary-color;
  }
  .item-right:hover > p:nth-child(2) {
    color: $primary-color;
  }
  .item-right:hover > p:nth-child(3) {
    color: $primary-color;
  }

  .item-right > p:nth-child(1),
  .item-right > p:nth-child(2) {
    transition: all 0.3s ease-in-out;
  }

  .item-right > p:nth-child(1) {
    font-size: $fs-20;
    font-weight: 600;
    color: $black;
    transition: all 0.3s ease-in-out;
  }

  .item-right > p:nth-child(2) {
    height: 90px;
    margin-top: 10px;
    line-height: 18px;
  }
  .item-right > p:nth-child(3) {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}
</style>
