import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Schedule from '@/views/Schedule.vue'
import News from '@/views/News.vue'
import NewsDetails from '@/views/NewsDetails.vue'
import DataBank from '@/views/DataBank.vue'
import Stadium from '@/views/Stadium.vue'
import EventGuide from '@/views/EventGuide.vue'
import History from '@/views/History.vue'
import Player from '@/views/Player.vue'
import Team from '@/views/Team.vue'
import Watch from '@/views/Watch.vue'
import TeamSchedule from '@/views/TeamSchedule.vue'
import TeamData from '@/views/TeamData.vue'
import TeamPlayer from '@/views/TeamPlayer.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/schedule',
    name: 'Schedule',
    component: Schedule,
  },
  {
    path: '/news',
    name: 'News',
    component: News,
  },
  {
    path: '/newsdetails/:id',
    name: 'NewsDetails',
    component: NewsDetails,
  },
  {
    path: '/databank',
    name: 'DataBank',
    component: DataBank,
  },
  {
    path: '/stadium',
    name: 'Stadium',
    component: Stadium,
  },
  {
    path: '/eventguide',
    name: 'EventGuide',
    component: EventGuide,
  },
  {
    path: '/history',
    name: 'History',
    component: History,
  },
  {
    path: '/player/:id',
    name: 'Player',
    component: Player,
  },
  {
    path: '/team/:id/',
    name: 'Team',
    component: Team,
    redirect: '/team/:id/comp',
    children: [
      {
        path: '/team/:id/comp',
        name: 'TeamSchedule',
        component: TeamSchedule,
      },
      {
        path: '/team/:id/data',
        name: 'TeamData',
        component: TeamData,
      },
      {
        path: '/team/:id/player',
        name: 'TeamPlayer',
        component: TeamPlayer,
      },
    ],
  },
  {
    path: '/watch/:id',
    name: 'Watch',
    component: Watch,
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '@/views/About.vue'),
  // },
]

const router = new VueRouter({
  mode: 'history',
  routes,
  // scrollBehavior 方法接收 to 和 from 路由对象。第三个参数 savedPosition 当且仅当 popstate 导航 (通过浏览器的 前进/后退 按钮触发) 时才可用。
  // 返回 savedPosition，在按下 后退/前进 按钮时，在滚动条位置，就会像浏览器的原生表现那样：
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
})

export default router
