<template>
  <div class="competition-box">
    <div class="level1-nav">
      <div
        class="nav-item"
        v-for="item of level1List"
        :key="item.id"
        @click="mainNavClick(item.id)"
        :class="{ 'level-active': level1Index === item.id }"
      >
        <p>{{ item.title }}</p>
      </div>
    </div>
    <div class="level2-nav" v-show="level1Index === 0">
      <div
        class="nav-item"
        v-for="item of level2List"
        :key="item.id"
        @click="subNavClick(item.id)"
        :class="{ 'level-active': level2Index === item.id }"
      >
        <p>{{ item.title }}</p>
      </div>
    </div>
    <div class="main-box">
      <div class="schedule">
        <div class="item" v-for="item of groupList" :key="item.ID">
          <span class="date">{{ item.Time }}</span>
          <span class="day">{{ item.Month }}-{{ item.Day }}</span>
          <span class="status">{{ item.IsBegin === 0 ? '未' : item.IsBegin === 1 ? '已开始' : '已结束' }}</span>
          <div class="team">
            <div class="red">
              <span>{{ item.AName }}</span>
              <img :src="item.AImg1" :alt="item.AName" />
            </div>
            <i>-</i>
            <div class="blue">
              <img :src="item.BImg1" :alt="item.BName" />
              <span>{{ item.BName }}</span>
            </div>
          </div>
          <a class="iconfont icon-jiantou2 to-watch"></a>
        </div>
      </div>
      <div class="score-bill">
        <div class="head">
          <div>排名</div>
          <div>国家</div>
          <div>胜</div>
          <div>平</div>
          <div>负</div>
          <div>净胜</div>
          <div>积分</div>
        </div>
        <div>
          <div class="bill-item" v-for="item of billList" :key="item.ID">
            <div>{{ item.Num }}</div>
            <div>
              <router-link :to="'/team/' + item.CountryID" class="router-link--reset">
                <img :src="item.CountryImg" alt="item.CountryName" />
                <span>{{ item.CountryName }}</span>
              </router-link>
            </div>
            <div>{{ item.Win }}</div>
            <div>{{ item.Level }}</div>
            <div>{{ item.Lose }}</div>
            <div>{{ item.NetWin }}</div>
            <div>{{ item.Goal }}</div>
          </div>
        </div>
        <div class="bill-rules">
          <p>小组赛出线规则：</p>
          <p>
            6个小组的头两名，以及成绩最好的4个小组第三将会晋级八分之一淘汰赛。如果小组内有球队的积分相同，将按照以下顺序决定出线球队：胜负关系->净胜球->进球->公平竞赛得分->欧足联排名。最为特殊的情况是，如果两支积分、得失球均相同的球队，在小组赛最后一轮相遇，当两支球队仍然不能分出结果时，将会进行点球决胜来决定晋级球队。
          </p>
          <p>在比较不同小组间第三名的排位时，将按照以下顺序决定出线球队：积分->净胜球->进球->公平竞赛得分->欧足联排名。</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getScheduleList } from '@/api/home'
export default {
  name: 'competition-box',
  data() {
    return {
      level1List: [
        {
          id: 0,
          title: '小组赛',
        },
        {
          id: 1,
          title: '1/8决赛',
        },
        {
          id: 2,
          title: '1/4决赛',
        },
        {
          id: 3,
          title: '半决赛',
        },
        {
          id: 4,
          title: '决赛',
        },
      ],
      level2List: [
        {
          id: 0,
          title: 'A组',
        },
        {
          id: 1,
          title: 'B组',
        },
        {
          id: 2,
          title: 'C组',
        },
        {
          id: 3,
          title: 'D组',
        },
        {
          id: 4,
          title: 'E组',
        },
        {
          id: 5,
          title: 'F组',
        },
      ],
      level1Index: 0,
      level2Index: 0,
      groupAllList: [],
      groupList: [],
      billAllList: [],
      billList: [],
    }
  },
  mounted() {
    this.initCPBox()
  },
  methods: {
    subNavClick(id) {
      this.level2Index = id
      this.getGroupData(id)
    },
    mainNavClick(id) {
      this.level1Index = id
      this.getGroupData(id, 'main')
    },
    async initCPBox() {
      const res = await getScheduleList()
      this.groupAllList = res.data.Data.ScheduleList
      this.billAllList = res.data.Data.List
      this.getGroupData(this.level2Index)
    },
    getGroupData(id, type = 'sub') {
      if (type === 'main' && id !== 0) {
        this.groupList = this.groupAllList.filter(item => item.Group1 === id)
        return
      }
      this.groupList = this.groupAllList.filter(item => item.Group1 === 0 && item.Group2 === id)
      this.billList = this.billAllList.filter(item => item.Group2 === id)[0].Data
    },
  },
}
</script>

<style lang="scss">
.competition-box {
  background-color: $white;
  padding: 20px;
  font-size: $fs-16;
  .level1-nav {
    display: flex;
    .nav-item {
      width: 125px;
      height: 36px;
      line-height: 36px;
      background-color: #f7f7f8;
      text-align: center;
      color: $text-color-scd;
      transform: skew(-30deg);
      cursor: pointer;
      &:not(:first-child) {
        margin-left: 10px;
      }
      p {
        transform: skew(30deg);
      }
    }
  }
  .level2-nav {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    .nav-item {
      flex: 1;
      height: 30px;
      line-height: 30px;
      background-color: #f7f7f8;
      text-align: center;
      color: $text-color-scd;
      cursor: pointer;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
  }
  .level-active {
    background-color: $primary-color !important;
    color: $white !important;
  }
}
.main-box {
  display: flex;
  margin-top: 15px;
}
.schedule {
  flex: 6;
  .item {
    height: 90px;
    display: flex;
    align-items: center;
    background-color: #f7f7f8;
    padding: 0 20px;
    position: relative;
    &:not(:first-child) {
      margin-top: 10px;
    }
    img {
      width: 40px;
      height: 40px;
      vertical-align: middle;
      border-radius: 50%;
    }
    .date {
      width: 12%;
    }
    .day {
      width: 12%;
      color: $text-color-scd;
      font-size: $fs-base;
    }
    .status {
      width: 12%;
    }
    .team {
      width: 50%;
      div {
        display: inline-block;
        margin-top: 5px;
      }
      .red {
        width: 33%;
        text-align: right;
        span {
          margin-right: 10px;
        }
      }
      .blue {
        width: 33%;
        text-align: left;
        span {
          margin-left: 10px;
        }
      }
      i {
        margin: 0 30px;
        font-weight: 600;
      }
      // span {
      //   &:nth-of-type(1) {
      //     margin-right: 10px;
      //   }
      //   &:nth-of-type(2) {
      //     margin-left: 10px;
      //   }
      // }
    }
    .to-watch {
      display: block;
      width: 20px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      background-color: $primary-color;
      border-radius: 2px;
      color: $white;
      font-size: $fs-12;
      font-weight: 600;
      position: absolute;
      right: 20px;
      top: 30px;
    }
  }
}
.score-bill {
  flex: 3;
  margin-left: 20px;
  .head,
  .bill-item {
    display: flex;
    align-items: center;
    & div:nth-child(1) {
      width: 18%;
      text-align: left;
    }
    & div:nth-child(2) {
      width: 32%;
      text-align: left;
      cursor: pointer;
      img {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        overflow: hidden;
        vertical-align: middle;
        margin-right: 5px;
      }
    }
    & div:nth-child(3) {
      width: 10%;
    }
    & div:nth-child(4) {
      width: 10%;
    }
    & div:nth-child(5) {
      width: 10%;
    }
    & div:nth-child(6) {
      width: 10%;
    }
    & div:nth-child(7) {
      width: 10%;
    }
    div {
      text-align: center;
    }
  }
  .head {
    padding: 15px 10px;
    background-color: #f7f7f8;
  }
  .bill-item {
    padding: 23px 10px;
    border-bottom: 1px solid #f7f7f8;
    color: $text-color-scd;
    & div:nth-child(1) {
      color: $text-color;
    }
    & div:nth-child(2) {
      font-size: $fs-base;
    }
  }
  .bill-rules {
    font-size: $fs-12;
    background-color: #f7f7f8;
    padding: 10px;
    margin-top: 22px;
    color: $text-color-scd;
    letter-spacing: 1.3px;
    border-radius: 2px;
    p {
      padding: 4px 0;
      line-height: 16px;
    }
  }
}
</style>
