<template>
  <div class="v-header">
    <header class="header">
      <div class="header-box">
        <div class="logo">
          <img src="../assets/images/pic/logo.png" alt="欧洲杯2021" />
          <div class="logo-des">
            <h1>欧洲杯2020</h1>
            <h3>European Cup</h3>
          </div>
        </div>
        <div class="header-main">
          <ul class="header-nav">
            <li v-for="(item, index) of headNavs" :key="item.id" @click="headNavLink(item.path)">
              <h3>{{ item.title }}</h3>
              <div class="line-wrapper"><p class="line" :class="{ 'line-active': navIndex === index }"></p></div>
            </li>
          </ul>
          <div class="header-user">
            <span class="iconfont icon-yonghu icon-user"></span>
            <span class="login" @click="onlogin">登录</span>
          </div>
        </div>
      </div>
    </header>
    <v-login :isShow="loginIsShow" @close="loginIsShow = false"></v-login>
    <v-message mType="warn" title="功能暂未开放" :isShow="isVMessShow" @close="isVMessShow = $event"></v-message>
  </div>
</template>

<script>
import VLogin from '@/components/v-login'
import VMessage from '@/components/v-message'
export default {
  name: 'v-header',
  props: {
    navIndex: {
      type: Number,
      default: 0,
    },
  },
  components: {
    VLogin,
    VMessage,
  },
  data() {
    return {
      headNavs: [
        {
          id: 0,
          title: '欧洲杯首页',
          path: '/',
        },
        {
          id: 1,
          title: '赛程赛果',
          path: '/schedule',
        },
        {
          id: 2,
          title: '资讯',
          path: '/news',
        },
        {
          id: 3,
          title: '资料库',
          path: '/databank',
        },
        {
          id: 4,
          title: '场馆',
          path: '/stadium',
        },
        {
          id: 5,
          title: '赛事指南',
          path: '/eventguide',
        },
        {
          id: 6,
          title: '历史',
          path: '/history',
        },
      ],
      loginIsShow: false,
      isVMessShow: false,
    }
  },
  methods: {
    headNavLink(path) {
      this.$router.push(path)
    },
    onlogin() {
      // this.loginIsShow = true
      this.isVMessShow = true
    },
  },
}
</script>

<style lang="scss">
.v-header {
  padding-top: 60px;
  .header {
    width: 100%;
    height: 60px;
    box-shadow: 0 0 2px 2px $br-color-base;
    background-color: $white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
  }
  .header-box {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    .logo {
      width: 180px;
      height: 100%;
      display: flex;
      align-items: center;
      margin-top: 12px;
      img {
        height: 80%;
        margin-top: 4px;
      }
    }
    .logo-des {
      flex: 1;
      margin-left: 10px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      color: #d1ccc0;
      h1 {
        font-size: $fs-20;
        font-weight: 600;
      }
      h3 {
        margin-top: 3px;
        font-size: $fs-16;
      }
    }
  }
  .header-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .header-nav {
    display: flex;
    font-size: $fs-18;
    // font-family: 幼圆;
    // font-weight: 600;
    li {
      height: 60px;
      line-height: 60px;
      margin-left: 35px;
      cursor: pointer;
      position: relative;
      .line-wrapper {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: center;
      }
      .line {
        width: 0%;
        height: 4px;
        border-radius: 2px;
        background-color: $primary-color;
        transition: width 0.2s;
        &-active {
          width: 100%;
        }
      }
      &:hover .line {
        width: 100%;
      }
    }
  }
  .header-user {
    .icon-user {
      color: #de1e30;
      font-weight: 600;
      vertical-align: middle;
      font-size: $fs-18;
    }
    .login {
      display: inline-block;
      width: 66px;
      height: 25px;
      line-height: 25px;
      border-radius: 2px;
      text-align: center;
      background-color: #de1e30;
      color: $white;
      font-size: $fs-12;
      margin-left: 18px;
      vertical-align: middle;
      cursor: pointer;
    }
  }
}
</style>
