<template>
  <div class="schedule">
    <v-header :navIndex="1"></v-header>
    <v-banner></v-banner>
    <main class="w-1200">
      <div class="schedule-nav">
        <div class="schedule-type">
          <div class="schedule-type--box">
            <div
              class="type-item"
              :class="{ 'type-item--active': scheduleNavIndex === index }"
              v-for="(item, index) of scheduleNavList"
              :key="item.sid"
              @click="scheduleNavClick(index, item.title)"
            >
              <p class="type-item--text">{{ item.title }}</p>
            </div>
          </div>
        </div>
        <div class="schedule-date">
          <div class="arrow-item" @click="leftArrowClick"><span class="iconfont icon-jiantou3 icon-arrow"></span></div>
          <div class="date-list--wrapper">
            <div class="date-list" ref="dateListRef">
              <div
                class="date-item no-select"
                :class="{ 'date-item--active': dateItemIndex === index }"
                v-for="(item, index) of dateList"
                :key="index"
                @click="dateItemClick(index)"
              >
                <p>{{ item.Week }}</p>
                <p>{{ item.Time }}</p>
                <p>{{ item.Count }}场</p>
              </div>
            </div>
          </div>
          <div class="arrow-item" @click="rightArrowClick"><span class="iconfont icon-jiantou2 icon-arrow"></span></div>
        </div>
      </div>
      <div class="schedule-list--bydate">
        <div class="schedule-item--bydate" v-for="(item, i) of scheduleList" :key="i">
          <div class="title">
            <p class="text">{{ item.date }}·{{ item.day }}·{{ item.type }}</p>
          </div>
          <div class="schedule-list-byself">
            <v-schedule-item v-for="(data, j) of item.subSdList" :key="j" :data="data" class="schedule-item-byself"></v-schedule-item>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import VBanner from '@/components/v-banner.vue'
import VHeader from '@/components/v-header.vue'
import VScheduleItem from '@/components/v-schedule-item.vue'
import { getScheduleList } from '@/api/schedule'
let dayList = []
export default {
  name: 'Schedule',
  components: {
    VBanner,
    VHeader,
    VScheduleItem,
  },
  data() {
    return {
      scheduleNavList: [
        {
          sid: 0,
          title: '全赛程',
        },
        {
          sid: 1,
          title: '热点赛事',
        },
        {
          sid: 2,
          title: 'A组',
        },
        {
          sid: 3,
          title: 'B组',
        },
        {
          sid: 4,
          title: 'C组',
        },
        {
          sid: 5,
          title: 'D组',
        },
        {
          sid: 6,
          title: 'E组',
        },
        {
          sid: 7,
          title: 'F组',
        },
        {
          sid: 8,
          title: '1/8决赛',
        },
        {
          sid: 9,
          title: '1/4决赛',
        },
        {
          sid: 10,
          title: '半决赛',
        },
        {
          sid: 11,
          title: '决赛',
        },
      ],
      scheduleNavIndex: 0,
      dateItemIndex: 0,
      timer: 0,
      scheduleAllList: [],
      scheduleList: [],
      scheduleSplitList: [],
      scheduleDateList: [],
      dateList: [],
    }
  },
  computed: {
    dateListRef() {
      return this.$refs.dateListRef
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll, false)
    this.initSchedule()
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    async initSchedule() {
      const res = await getScheduleList()
      this.scheduleAllList = res.data.Data.ScheduleList
      this.scheduleDateList = res.data.Data.ScheduleListGroup
      // console.log('scheduleAllList:', this.scheduleAllList)
      // console.log('scheduleDateList:', this.scheduleDateList)
      this.dateList = this.scheduleDateList[0].Data
      this.dateList.forEach(item => {
        dayList.push(item.Time)
      })
      this.filterScheduleList()
    },
    filterScheduleList() {
      /**
          var arr = [
            {
              date: '2012-02-01',
              day: '星期三',
              type: '小组赛',
              scheduleList: [{}, {}, ...],
            },
            ...
          ]
        */
      const tempSdList = []
      const dateKeys = []
      this.scheduleAllList.forEach(item => {
        const tempSdItem = {}
        const date = `${item.Year}年${item.Month}月${item.Day}日`
        const day = item.Week
        const type = item.Remarks
        const subSdList = []

        const key = dateKeys.indexOf(date)
        if (key === -1) {
          tempSdItem['date'] = date
          tempSdItem['day'] = day
          tempSdItem['type'] = type

          subSdList.push(item)
          tempSdItem['subSdList'] = subSdList
          tempSdList.push(tempSdItem)
          dateKeys.push(date)
        } else {
          tempSdList[key].subSdList.push(item)
        }
      })
      this.scheduleList = tempSdList
    },
    scheduleNavClick(index, title) {
      this.scheduleNavIndex = index
      this.dateList = this.scheduleDateList.filter(item => item.Group2 === index)[0].Data
      if (index === 0) {
        this.filterScheduleList()
        return
      }
      let tempSdByGroupList = []
      if (index === 1) {
        tempSdByGroupList = this.scheduleAllList.filter(item => item.IsHotSpot === true)
      } else {
        tempSdByGroupList = this.scheduleAllList.filter(item => item.GroupValue === title)
      }

      const tempSdSplitList = []
      const dateKeys = []
      tempSdByGroupList.forEach(item => {
        const tempSdItem = {}
        const date = `${item.Year}年${item.Month}月${item.Day}日`
        const day = item.Week
        const type = item.Remarks
        const subSdList = []

        const key = dateKeys.indexOf(date)
        if (key === -1) {
          tempSdItem['date'] = date
          tempSdItem['day'] = day
          tempSdItem['type'] = type
          subSdList.push(item)
          tempSdItem['subSdList'] = subSdList
          tempSdSplitList.push(tempSdItem)
          dateKeys.push(date)
        } else {
          tempSdSplitList[key].subSdList.push(item)
        }
      })
      this.scheduleList = tempSdSplitList
    },
    handleScroll() {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        // console.log('handleScroll')
        const scheduleItemList = [...document.querySelectorAll('.schedule-item--bydate')]
        const toPageTopList = scheduleItemList.map(item => item.offsetTop)
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
        let index = 0
        for (let i = 0; i < toPageTopList.length; i++) {
          if (scrollTop > toPageTopList[i] - 100) {
            index = i
          }
        }
        this.dateItemIndex = index
        if (index > 10) {
          this.rightArrowClick()
        } else {
          this.leftArrowClick()
        }
      }, 100)
    },
    // 处理粘性布局效果--待处理-暂用css实现
    handleSticky() {
      console.log("I'll deal with the sticky layout later...")
    },
    dateItemClick(i) {
      // this.dateItemIndex = index
      // const scheduleItemList = [...document.querySelectorAll('.schedule-item--bydate')]
      // scheduleItemList[index].scrollIntoView({ block: 'center' }) // 兼容性差且在此项目中有bug
      const scheduleItemList = [...document.querySelectorAll('.schedule-item--bydate')]
      const toPageTopList = scheduleItemList.map(item => item.offsetTop)
      window.scrollTo({ top: toPageTopList[i], left: 0, behavior: 'smooth' }) // 不考虑ie基本能用
    },
    rightArrowClick() {
      this.dateListRef.setAttribute('style', `transform:translateX(-1056px)`)
    },
    leftArrowClick() {
      this.dateListRef.setAttribute('style', `transform:translateX(0)`)
    },
  },
}
</script>

<style lang="scss">
.schedule {
  .schedule-nav {
    background-color: $white;
    padding: 20px 0;
    border-radius: 4px;
    // 因其兼容性问题，此粘性布局属性暂时仅作测试所用
    // 最终还是用js实现类似的粘性布局效果-tip-tip-tip
    position: sticky;
    top: 60px;
    z-index: 999;
  }
  .schedule-type {
    width: 1150px;
    height: 60px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
  }
  .schedule-type--box {
    width: 1200px;
    position: absolute;
    left: -25px;
    top: 0;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    .type-item {
      width: 92px;
      height: 36px;
      background-color: $secondary-color;
      color: $text-color-scd;
      transform: skew(-35deg);
      cursor: pointer;
      font-size: $fs-16;
      &--active {
        background-color: $primary-color;
        color: $white;
      }
    }
    .type-item--text {
      width: 92px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      transform: skew(35deg);
    }
  }
  .schedule-date {
    display: flex;
    justify-content: space-between;
    padding: 0 25px;
    .arrow-item {
      width: 35px;
      height: 118px;
      line-height: 118px;
      text-align: center;
      background-color: $secondary-color;
      cursor: pointer;
      .icon-arrow {
        color: $primary-color;
        font-size: $fs-22;
      }
    }
    .date-list--wrapper {
      flex: 1;
      overflow: hidden;
      margin: 0 12px;
    }
    .date-list {
      height: 100%;
      display: flex;
      align-items: center;
      // overflow-x: auto;
      transition: transform 0.4s linear;
      .date-item {
        flex-shrink: 0;
        width: 94px;
        height: 100px;
        background-color: $secondary-color;
        border-radius: 5px;
        padding: 10px 0;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        cursor: pointer;
        &:not(:first-child) {
          margin-left: 2px;
        }
        p:nth-child(2) {
          font-weight: 600;
          font-size: $fs-16;
        }
        &--active {
          background-color: $primary-color;
          color: $white;
        }
      }
    }
  }
  .schedule-item--bydate {
    background-color: $white;
    margin-top: 20px;
    border-radius: 3px;
    overflow: hidden;
    .title {
      width: auto;
      height: 0;
      line-height: 42px;
      color: $white;
      font-size: $fs-18;
      border-width: 0 42px 42px 0;
      border-style: solid;
      border-color: transparent transparent $primary-color transparent;
      display: inline-block;
      transform: rotateX(180deg);
      padding: 0 10px;
      .text {
        transform: rotateX(180deg);
      }
    }
  }
  .schedule-list-byself {
    padding: 20px;
  }
  .schedule-item-byself {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}
</style>
