    },
    handleMapClick(params) {
      // console.log('city:', params)

      let longitude = params.value[0]
      let latitude = params.value[1]
      let courtIndex = params.dataIndex
      let len = courtControl.length
      let cid = params.data.cid

      scatterData[courtIndex].sdi = 1 // 选中状态
      let selectIndex = courtControl.indexOf(courtIndex)
      let tempCourt = courtsList.filter(item => item.cid === cid)[0]
      switch (len) {
        case 0:
          courtControl.push(courtIndex) // 选中标识
          lngAndLatStart['coord'] = [longitude, latitude] // start坐标

          // fisrt court show
          this.courtLength = 1
          // 此处给第一个court赋值
          this.courtInfo1 = tempCourt

          this.cid1 = cid
          break
        case 1:
          if (selectIndex !== -1) {
            /* 重复点击第一个court（当前只有一个court被选中） */
            scatterData[courtIndex].sdi = 0 // 取消选中状态
            courtControl.splice(selectIndex, 1)
            lngAndLatStart['coord'] = [] // 清空start坐标

            // fisrt court hidden
            this.courtLength = 0
            // 此处置空第一个court
            this.courtInfo1 = {}

            this.cid1 = 0
          } else {
            /* 直接点击第二个court */
            courtControl.push(courtIndex) // 选中标识
            lngAndLatEnd['coord'] = [longitude, latitude] // End坐标
            options.series[1].data = [[lngAndLatStart, lngAndLatEnd]]

            // second court show
            this.courtLength = 2
            // 此处给第二个court赋值
            this.courtInfo2 = tempCourt
            this.cid2 = cid
            const tpFlDur = flightDuration.find(item => item.Venue1 === this.cid1 && item.Venue2 === this.cid2)
            this.flDur = tpFlDur.Duration
          }
          break
        case 2:
          if (selectIndex === 0) {
            /* 重复点击第一个court（当前有两个court被选中）*/
            scatterData[courtIndex].sdi = 0 // 取消选中状态
            courtControl.splice(selectIndex, 1)
            lngAndLatStart['coord'] = lngAndLatEnd['coord']
            options.series[1].data = []

            this.courtLength = 1
            // 此处将第二个court赋值给第一个court并将第二个court置空
            this.courtInfo1 = this.courtInfo2
            this.courtInfo2 = {}
            this.cid1 = this.cid2
            this.cid2 = 0
          } else if (selectIndex === 1) {
            /* 重复点击第二个court（当前有两个court被选中）*/
            scatterData[courtIndex].sdi = 0 // 取消选中状态
            courtControl.splice(selectIndex, 1)
            lngAndLatEnd['coord'] = []

            // second court hidden
            this.courtLength = 1
            // 此处置空第二个court
            this.courtInfo2 = {}
            this.cid2 = 0
          } else {
            /* 直接点击第三个court */
            scatterData[courtControl[0]].sdi = 0 // 取消选中状态
            scatterData[courtControl[1]].sdi = 0 // 取消选中状态
            courtControl = [] // 清空court数组
            lngAndLatEnd['coord'] = [] // 清空End坐标

            courtControl.push(courtIndex) // 选中标识
            lngAndLatStart['coord'] = [longitude, latitude] // start坐标

            this.courtLength = 1
            // 此处给第一个court赋值，置空第二个court
            this.courtInfo1 = tempCourt
            this.courtInfo2 = {}
            this.cid1 = cid
            this.cid2 = 0
          }
          break
      }
      mapChart.setOption(options)
    },
    courtCloseClick() {
      this.courtLength = 0
      scatterData.forEach(item => item.sdi !== 0 && (item.sdi = 0))
      options.series[1].data = []
      lngAndLatStart['coord'] = []
      lngAndLatEnd['coord'] = []
      courtControl = []
      this.courtInfo1 = {}
      this.courtInfo2 = {}
      this.cid1 = 0
      this.cid2 = 0
      mapChart.setOption(options)
    },
    handleDestory() {
      // 手动触发 destroy 相关的生命周期
      this.$destroy()
    },
  },
