<template>
  <div class="v-pie">
    <div :id="pid" style="{width:140px;height:140px;}"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts/core'
import { TooltipComponent } from 'echarts/components'
import { PieChart } from 'echarts/charts'
import { CanvasRenderer } from 'echarts/renderers'
echarts.use([TooltipComponent, PieChart, CanvasRenderer])
var pieChart = null
export default {
  name: 'v-pie',
  props: {
    pid: Number,
  },
  mounted() {
    this.initPie()
  },
  beforeDestroy() {
    echarts.dispose(pieChart)
  },
  methods: {
    initPie() {
      var chartDom = document.getElementById(this.pid)
      pieChart = echarts.init(chartDom)
      var option = {
        tooltip: {
          trigger: 'item',
        },
        color: ['#F7F7F7', '#e64f5f', '#3fbdd0'],
        series: [
          {
            name: '近十场胜负',
            type: 'pie',
            radius: ['70%', '80%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
            },
            emphasis: {
              label: {
                show: false,
              },
              scale: false,
            },
            data: [
              { name: '平', value: 1048 },
              { name: '胜', value: 735 },
              { name: '负', value: 580 },
            ],
          },
        ],
      }
      option && pieChart.setOption(option)
    },
  },
}
</script>

<style></style>
