<template>
  <div class="v-video-room">
    <div class="watch-box">
      <div id="player"></div>
      <div v-if="!hasLive" class="player--nolive">暂无直播~</div>
    </div>
    <div class="chat-box">
      <header>
        <span>聊天室</span>
        <span>
          <i class="iconfont icon-yonghu icon-ucount"></i>
          <i>{{ connectCount }}</i>
        </span>
      </header>
      <main ref="chatListRef" id="chatbox">
        <ul class="chat-list">
          <li class="chat-item" v-for="(item, index) of chatList" :key="index">
            <div v-if="item.type === 'chat'">
              <span class="name">{{ item.name }}：</span>
              <span class="speak">{{ item.words }}</span>
            </div>
            <div v-else>
              <span style="color:#3fbdd0">欢迎&nbsp;&nbsp;&nbsp;{{ item.visitorName }}&nbsp;&nbsp;&nbsp;进入直播间</span>
            </div>
          </li>
        </ul>
      </main>
      <footer>
        <div class="speak-box">
          <div
            ref="richInpRef"
            class="v-rich-input__body"
            contenteditable="true"
            placeholder="恕我直言..."
            @keydown.enter.prevent="emitSpeackWords"
          ></div>
        </div>
        <div class="emit-box">
          <button @click="emitSpeackWords">发送</button>
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import DPlayer from 'dplayer'
import Hls from 'hls.js'
import Flv from 'flv.js'

// const WS_PATH = 'ws://localhost:3000/'
const WS_PATH = 'ws://155.159.71.67:3000/'
// let ws = null

// let reconnTimer = null
// let lockReconnect = false // 一把锁，用来避免在某些情况下短时间内重复执行reconnect函数
// let isReonnect = true // 一把锁，控制页面进入beforeDestroy钩子时是否执行重连（正常关闭页面不重连）
/**
 * 以上这三个变量都不能放在这里初始化，应该放在data里。
 * 场景：首页和观看直播页都引用了此组件，并且首页有导航可以进入观看直播页。
 * 以isReonnect变量举例：经测试，在首页点击导航进入直播页，此组件生命周期执行顺序为：
 * v-video-room-created // 直播页v-video-room的created钩子
   v-video-room-created--isReonnect: true
   beforeDestroy // 首页v-video-room的created钩子
   v-video-room-beforeDestroy--isReonnect: false
   v-video-room-mounted // 直播页v-video-room的mounted钩子
   v-video-room-mounted--isReonnect: true
   ws disconnected!!! // 此时才执行在首页v-video-room的beforeDestroy钩子中调用的wsClose方法。
   wsClose-isReonnect: true
   而且当从首页到直播页时，此组件这里的代码不会执行（生命周期倒是会执行），所以相当于这里的isReonnect变量被共用了，
   再加上上面奇怪的生命周期执行顺序，就会影响到对isReonnect的判定。
   将isReonnect放在data里可保证每个组件都拥有自己的isReonnect变量，不会相互干扰。
 */
export default {
  name: 'v-video-room',
  props: {
    url: {
      type: String,
      require: true,
    },
    isLive: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      ws: null,
      speakWords: '',
      chatList: [],
      visitorName: '',
      connectCount: 0,
      roomId: 0,
      reconnTimer: null,
      lockReconnect: false,
      isReonnect: true,
      hasLive: false,
    }
  },

  computed: {
    richInp() {
      return this.$refs.richInpRef
    },
    chatListC() {
      return this.$refs.chatListRef
    },
  },
  mounted() {
    this.initWs()
    if (!this.url) {
      this.hasLive = false
    } else {
      this.hasLive = true
      this.initDPlayer()
    }
    this.richInp.addEventListener('paste', this.InputPasteFn)
  },
  beforeDestroy() {
    this.richInp.removeEventListener('paste', this.InputPasteFn)
    this.isReonnect = false
    this.reconnTimer && clearTimeout(this.reconnTimer)
    this.ws && this.ws.close()
  },
  watch: {
    url(newV) {
      if (!newV) {
        this.hasLive = false
      } else {
        this.hasLive = true
        this.initDPlayer()
      }
    },
  },
  methods: {
    InputPasteFn(e) {
      // console.table('paste-e.clipboardData:', e.clipboardData)
      // console.table('paste-e.clipboardData.items:', e.clipboardData.items)
      e.preventDefault()
      var text = (e.clipboardData || window.clipboardData).getData('text')
      // console.log('paste-text:', text)
      var sel = window.getSelection()
      if (sel && sel.rangeCount > 0) {
        var rangeObj = sel.getRangeAt(0)
        var textNode = document.createTextNode(text)
        // sel.deleteFromDocument();
        rangeObj.deleteContents()
        rangeObj.insertNode(textNode)
        rangeObj.collapse(false)
        sel.addRange(rangeObj)
      }
    },
    initDPlayer() {
      const url = this.url
      const regxURL = this.decideLiveURL(url)
      if (!regxURL) {
        this.hasLive = false
        return
      }
      let videoOptions = null
      const isLive = this.isLive
      switch (regxURL) {
        case '.m3u8':
          videoOptions = {
            url,
            type: 'customHls',
            customType: {
              customHls: function(video, player) {
                const hls = new Hls()
                hls.loadSource(video.src)
                hls.attachMedia(video)
              },
            },
          }
          break
        case '.flv':
          videoOptions = {
            url,
            type: 'customFlv',
            customType: {
              customFlv: function(video, player) {
                const flvPlayer = Flv.createPlayer({
                  type: 'flv',
                  url: video.src,
                })
                flvPlayer.attachMediaElement(video)
                flvPlayer.load()
              },
            },
          }
          break
        case '.mp4':
          videoOptions = {
            url,
          }
          break
      }
      const dp = new DPlayer({
        container: document.getElementById('player'),
        video: videoOptions,
        autoplay: true,
        loop: true,
        live: isLive,
      })
    },
    decideLiveURL(url) {
      const REGX_VIDEO_URL = /\.(m3u8|flv|mp4)/i
      let regxURL = url.match(REGX_VIDEO_URL)
      if (!regxURL) return
      regxURL = regxURL[0].toLowerCase()
      return regxURL
    },
    // 重连（注意重连频率问题）
    reconnect() {
      /**
				* 经测试（关闭服务器->会触发客户端WebSocket的onclose监听事件），(在不限制触发频率的情况下)谷歌浏览器上大约每两秒就会触发一次onclose监听事件，即每大约两秒就会执行reconnect函数重连一次。
        * 还有在某些情况下可能会同时触发wsError、wsClose事件，这两个事件里都会执行重连，所以会重复执行。
				* 不限制频率直接重连，代码如下
				* 	// console.log('断线正在重连中...')
						// initWs()
				* 如果不做限制的话，重连频率有些过快了，因此用setTimeout限制一下重连频率。
			*/
      // 如果lockReconnect为true，说明现在现在任务队列中已经有一个定时器在等待倒计时准备重连了，就终止代码执行，避免再生成一个定时器重复执行重连操作
      if (this.lockReconnect) return
      this.lockReconnect = true
      // reconnTimer && clearTimeout(reconnTimer)
      this.reconnTimer = setTimeout(() => {
        console.log('断线正在重连中...')
        this.initWs()
        console.log('这是一次重连')
        // 本次重连完成（不管连没连上），都将lockReconnect锁释放（置为false），这样如果没连上就能再次进入reconnect函数执行重连了
        this.lockReconnect = false
      }, 10000)
    },
    // onRichInput(event) {
    //   this.speakWords = event.target.innerHTML
    // },
    emitSpeackWords() {
      if (!this.richInp.innerHTML) return
      this.ws.send(JSON.stringify({ type: 'chat', name: this.visitorName, words: this.richInp.innerHTML, roomId: this.roomId }))
      this.richInp.innerHTML = ''
    },
    initWs() {
      if (typeof WebSocket === undefined) {
        alert('Your browser does not support WebSocket, you cannot use the chat function, please download other modern browsers')
        return
      }
      this.roomId = window.location.href.split('/').pop()
      this.ws = new WebSocket(WS_PATH + this.roomId)
      this.ws.addEventListener('open', this.wsOpen)
      this.ws.addEventListener('message', this.wsMessage)
      this.ws.addEventListener('close', this.wsClose)
      this.ws.addEventListener('error', this.wsError)
    },
    wsOpen() {
      console.log('ws connect success!!!')
    },
    wsMessage(message) {
      // console.log('message:', message.data) // 接收到的是string类型
      const msg = JSON.parse(message.data)
      const type = msg.type
      switch (type) {
        case 'assignVisitorName': {
          const visitorName = msg.visitorName
          this.visitorName = visitorName
          break
        }
        case 'visitor': {
          const visitorName = msg.visitorName
          const chatItem = { type, visitorName }
          this.chatList.push(chatItem)
          this.$nextTick(() => {
            this.chatListC.scrollTop = this.chatListC.scrollHeight
          })
          break
        }
        case 'connCount': {
          this.connectCount = msg.connectCount
          break
        }
        case 'chat': {
          const { name, words } = msg
          const chatItem = { type, name, words }
          this.chatList.push(chatItem)
          this.$nextTick(() => {
            this.chatListC.scrollTop = this.chatListC.scrollHeight
          })
          break
        }
        case 'chatHis': {
          const chatHisList = msg.chatHisList
          this.chatList.push(...chatHisList)
          this.$nextTick(() => {
            this.chatListC.scrollTop = this.chatListC.scrollHeight
          })
          break
        }
      }
    },
    wsClose() {
      console.log('ws disconnected!!!')
      if (this.isReonnect) {
        this.reconnect()
      }
    },
    wsError() {
      console.log('ws error')
      this.reconnect()
    },
  },
}
</script>

<style lang="scss">
.v-video-room {
  display: flex;
  height: 640px;
  margin-top: 15px;
  background-color: $white;
  .watch-box {
    position: relative;
    flex: 5;
    background-color: black;
  }
  #player {
    width: 100%;
    height: 100%;
  }
  .player--nolive {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    height: 44px;
    line-height: 44px;
    text-align: center;
    color: #ccc;
  }
  .chat-box {
    flex: 2;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 6px #d1ccc0;
    header {
      height: 42px;
      background-color: $white;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 10px;
      color: $text-color-scd;
      .icon-ucount {
        font-weight: 600;
        margin-right: 5px;
        font-size: $fs-12;
      }
    }
    main {
      flex: 0 1 478px;
      background-color: #f5f5f5;
      padding: 0 8px;
      box-sizing: border-box;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 2px;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #dcdde1;
      }
    }
    .chat-list {
      .chat-item {
        padding: 7px 0;
        font-size: $fs-12;
        .name {
          color: $primary-color;
        }
        .speak {
          line-height: 16px;
          letter-spacing: 1px;
        }
      }
    }
    footer {
      height: 120px;
      .speak-box {
        width: 100% !important;
        height: calc(100% - 40px);
        box-sizing: border-box;
        padding: 5px;
      }
      .v-rich-input {
        width: 100%;
        height: 100%;
      }
      .v-rich-input__body {
        width: 100%;
        height: 100%;
        outline: none;
        &:empty::before {
          content: attr(placeholder);
          color: $text-color-scd;
        }
      }
      .emit-box {
        height: 40px;
        line-height: 40px;
        text-align: right;
        button {
          width: 60px;
          height: 24px;
          line-height: 24px;
          text-align: center;
          background-color: #ffa03b;
          border-radius: 12px;
          border: 0;
          color: $white;
          font-size: $fs-12;
          margin-right: 6px;
        }
      }
    }
  }
}
</style>
