import axios from 'axios'

const service = axios.create({
  baseURL: 'http://160.121.91.158',
  // baseURL: 'http://172.16.189.48/xxyy3',
  timeout: 8000,
  // headers: {'X-Custom-Header': 'foobar'}
})

// 请求拦截器
service.interceptors.request.use(
  function(config) {
    // 在这里可以做一些 请求发送之前 要做的事
    // 比如当用户登录后可以在此将token加入到请求头中
    // eg: if (user) config.headers['Authentication'] = 'token'
    return config
  },
  function(error) {
    // 在此处理请求错误
    return Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(
  function(response) {
    // 在这里可以做一些 收到请求响应之后 要做的事
    // console.log('service-response:', response.data) // 每次收到响应后在控制台打印出响应数据
    return response
  },
  function(error) {
    // 在此处理响应错误
    return Promise.reject(error)
  }
)

export default service
