<template>
  <div class="player-bill-box">
    <div class="player-tabs">
      <div class="tab" @click="shooterClick" :class="{ activeBg: playerTabIndex === 0 }">
        <p :class="{ activeBg: playerTabIndex === 0 }" style="marginLeft:-15px">射手榜</p>
      </div>
      <div class="tab" @click="assistClick" :class="{ activeBg: playerTabIndex === 1 }">
        <p :class="{ activeBg: playerTabIndex === 1 }" style="marginLeft:15px">助攻榜</p>
      </div>
    </div>
    <div class="player-list">
      <div class="player-item" v-for="(item, index) of billList" :key="index">
        <div class="item-main" @click="playerItemIndex = index">
          <div class="item-left">
            <span class="num">{{ item.Sort }}</span>
            <span>{{ item.PlayerName }}</span>
          </div>
          <div class="item-right">
            <span class="goals">{{ item.Sum }}球</span>
            <span class="iconfont icon-jiantou1"></span>
          </div>
        </div>
        <div class="item-extra" v-show="playerItemIndex === index">
          <img :src="item.CountryImg" alt="" />
          <span>{{ item.CountryName }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getPlayerRankingList } from '@/api/home'
export default {
  name: 'player-bill-box',
  data() {
    return {
      playerTabIndex: 0,
      playerItemIndex: 0,
      billList: [],
      AssistsList: [],
      ShooterList: [],
    }
  },
  async created() {
    const res = await getPlayerRankingList()
    const { AssistsList, ShooterList } = res.data.Data
    this.AssistsList = AssistsList
    this.ShooterList = ShooterList
    this.billList = ShooterList
  },
  methods: {
    shooterClick() {
      this.playerTabIndex = 0
      this.billList = this.ShooterList
    },
    assistClick() {
      this.playerTabIndex = 1
      this.billList = this.AssistsList
    },
  },
}
</script>

<style lang="scss">
.player-bill-box {
  background-color: $white;
  border-radius: 5px;
  overflow: hidden;
  .player-tabs {
    display: flex;
    background-color: $white;
    .tab {
      flex: 1;
      text-align: center;
      height: 36px;
      line-height: 36px;
      color: $text-color-scd;
      font-size: $fs-16;
      transform: skew(-35deg);
      cursor: pointer;
    }
    p {
      transform: skew(35deg);
    }
    .activeBg {
      background-color: $primary-color;
      color: $white;
    }
  }
  .player-list {
    height: 534px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $primary-color;
    }
  }
  .player-item {
    cursor: pointer;
  }
  .item-main {
    display: flex;
    justify-content: space-between;
    padding: 15px 8px;
    .item-left {
      .num {
        margin-right: 8px;
      }
    }
    .item-right {
      .goals {
        margin-right: 8px;
      }
    }
  }
  .item-extra {
    padding: 6px 24px;
    img {
      width: 22px;
      height: 22px;
      vertical-align: middle;
      margin-right: 8px;
    }
  }
}
</style>
