import service from '@/utils/service'

/* 新闻列表数据
 * @param  {Object} data 请求参数
 * @return {Object|Promise} 响应结果（一个Promise）
 */
export function getNewsList(data) {
  return service.request({
    url: `/api/EuropeInput/GetAdvisoryList`,
    method: 'POST',
    data,
  })
}

/**
 * 新闻详情数据
 * @param  {Object} data 请求参数 包含新闻ID
 * @return {Object|Promise} 响应结果（一个Promise）
 */
export function getNesDetails(data) {
  return service.request({
    url: `/api/EuropeInput/GetAdvisoryOnly`,
    method: 'POST',
    data,
  })
}

