<template>
  <div class="v-login" v-if="isShow">
    <div class="container">
      <div class="close iconfont icon-guanbi" @click="loginClose"></div>
      <div v-if="isLoginWrap" class="login-box">
        <h3>登录</h3>
        <form>
          <div class="form-item"><input type="text" placeholder="请输入手机号" /></div>
          <div class="form-item"><input type="text" placeholder="请输入密码" /></div>
          <div class="tools-item">
            <span class="auto-login"><input type="checkbox" id="auto" /><label for="auto">自动登陆</label></span>
            <span class="forget-pwd">忘记密码？</span>
          </div>
          <div class="login-btn" @click="loginClick">登录</div>
        </form>
        <div class="go-regis" @click="isLoginWrap = false">注册账号</div>
      </div>
      <div v-else class="login-box regis-box">
        <h3>注册</h3>
        <form>
          <div class="form-item"><input type="text" placeholder="请输入11位数手机号" @blur="phoneBlur" v-model="phone" /></div>
          <!-- ---------------------- -->
          <div class="valid-err_tip" v-if="!phoneCorrect"><i class="iconfont icon-jinggao err-tip-icon"></i>请输入正确手机号</div>
          <div class="valid-correct_tip" v-else><i class="iconfont icon-zhengque correct-tip-icon"></i>验证成功</div>
          <!-- ---------------------- -->
          <div class="form-item">
            <input type="text" placeholder="创建密码" @blur="pwdBlur" v-model="pwd" />
          </div>
          <!-- ---------------------- -->
          <div class="valid-err_tip" v-if="!pwdCorrect">
            <i class="iconfont icon-jinggao err-tip-icon"></i>密码由字母、数字、符号中的至少两种组成，不包含空格,不能输入中文
          </div>
          <div class="valid-correct_tip" v-else><i class="iconfont icon-zhengque correct-tip-icon"></i>验证成功</div>
          <!-- ---------------------- -->
          <div class="tools-item">
            <input type="text" placeholder="请输入验证码" />
            <a @click="getValiCode">{{ clock === 0 ? '获取验证码' : clock + 's后再获取' }}</a>
          </div>
          <div class="login-btn" @click="registerClick" v-if="phoneCorrect && pwdCorrect">注册</div>
          <div class="login-btn-disabled" v-else>注册</div>
        </form>
        <div class="go-regis" @click="isLoginWrap = true">立即登录</div>
      </div>
    </div>
    <v-message
      mType="warn"
      title="这是warn提示框！可传入其他主题样式！"
      :isShow="isVMessShow"
      @close="isVMessShow = $event"
    ></v-message>
  </div>
</template>

<script>
const PHONE_REGEX = /^1[3-9]\d{9}$/
// 至少为字母、数字、符号中的两种组成，不包含空格,不能输入中文
const PWD_REGX = /(?!^\d+$)(?!^[A-Za-z]+$)(?!^[^A-Za-z0-9]+$)(?!^.*[\u4E00-\u9FA5].*$)^\S{6,20}$/
import VMessage from '@/components/v-message'
export default {
  name: 'v-login',
  components: {
    VMessage,
  },
  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isLoginWrap: true,
      clock: 0,
      phone: '',
      pwd: '',
      phoneCorrect: false,
      pwdCorrect: false,
      isVMessShow: false,
    }
  },
  methods: {
    loginClick() {
      this.isVMessShow = true
    },
    loginClose() {
      this.$emit('close', false)
      this.isVMessShow = false
    },
    registerClick() {
      const isPhoneSuccess = this.phoneValid()
      const isPwdSuccess = this.pwdValid()
      if (!isPhoneSuccess) {
        this.phoneCorrect = false
      } else if (!isPwdSuccess) {
        this.pwdCorrect = false
      } else {
        alert('register success!')
        this.resetForm()
      }
    },
    resetForm() {
      this.phone = ''
      this.pwd = ''
      this.phoneCorrect = false
      this.pwdCorrect = false
    },
    pwdBlur() {
      const isSuccess = this.pwdValid()
      this.pwdCorrect = isSuccess
    },
    phoneBlur() {
      const isSuccess = this.phoneValid()
      this.phoneCorrect = isSuccess
    },
    pwdValid() {
      return PWD_REGX.test(this.pwd)
    },
    phoneValid() {
      return PHONE_REGEX.test(this.phone)
    },
    getValiCode() {
      if (this.clock !== 0) return
      this.clock = 10
      const timer = setInterval(() => {
        --this.clock
        this.clock <= 0 && clearInterval(timer)
      }, 1000)
    },
  },
}
</script>

<style lang="scss">
.v-login {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99999;
  background-color: $disabled-color;
  .container {
    width: 400px;
    height: 480px;
    background-color: $white;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 3px;
    padding: 30px 60px;
    box-sizing: border-box;
  }
  .login-box {
    h3 {
      height: 40px;
      text-align: center;
      font-size: $fs-20;
      border-bottom: 2px solid $primary-color;
      color: $h-color;
      font-weight: 600;
    }
    .form-item {
      margin-top: 30px;
      input {
        width: 100%;
        height: 34px;
        outline: none;
        box-sizing: border-box;
        border: 1px solid $br-color-base;
        padding-left: 5px;
      }
    }
    .valid-correct_tip {
      color: $success-color;
      font-size: $fs-12;
      margin-top: 5px;
      .correct-tip-icon {
        vertical-align: middle;
        margin-right: 3px;
      }
    }
    .valid-err_tip {
      color: $error-color;
      font-size: $fs-12;
      margin-top: 5px;
      .err-tip-icon {
        vertical-align: middle;
        margin-right: 3px;
      }
    }
    .tools-item {
      height: 34px;
      margin-top: 30px;
      -moz-user-select: none; /*火狐*/
      -webkit-user-select: none; /*webkit浏览器*/
      user-select: none;
      .auto-login {
        float: left;
        vertical-align: middle;
        input,
        label {
          vertical-align: middle;
        }
      }
      .forget-pwd {
        float: right;
        cursor: pointer;
      }
    }
    .login-btn,
    .login-btn-disabled {
      height: 34px;
      height: 34px;
      line-height: 34px;
      text-align: center;
      background-color: $primary-color;
      color: $white;
      margin-top: 20px;
      cursor: pointer;
    }
    .login-btn-disabled {
      background-color: $disabled-color;
    }
    .go-regis {
      color: $primary-color;
      text-align: center;
      margin-top: 50px;
      cursor: pointer;
    }
  }
  .regis-box {
    .tools-item {
      input {
        width: 55%;
        height: 34px;
        outline: none;
        box-sizing: border-box;
        border: 1px solid $br-color-base;
        padding-left: 5px;
      }
      a {
        display: block;
        width: 40%;
        height: 34px;
        line-height: 34px;
        text-align: center;
        background-color: $disabled-color;
        float: right;
      }
    }
  }
  .close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: $fs-20;
    cursor: pointer;
    color: $primary-color;
    transition: transform 0.3s linear;
    &:hover {
      transform: rotate(360deg);
    }
  }
}
</style>
