<template>
  <div class="team-schedule">
    <table>
      <thead>
        <tr>
          <th>赛事</th>
          <!-- <th>轮次</th> -->
          <th>时间</th>
          <th>主队</th>
          <th>比分</th>
          <th>客队</th>
          <th>半场</th>
          <th>功能</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item of schedules" :key="item.ID">
          <td>{{ item.Match }}</td>
          <!-- <td>第十轮</td> -->
          <td>{{ item.Time }}</td>
          <td>{{ item.AwayGame }}</td>
          <td class="emphasis">-</td>
          <td>{{ item.HomeCourt }}</td>
          <td>-</td>
          <td>
            <a>分析</a>
            <a>走势</a>
            <a>历史</a>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="mt-15">
      <v-paging :amount="amount" :maxCount="6" @change-page="chagePage"></v-paging>
    </div>
  </div>
</template>

<script>
import VPaging from '@/components/v-paging'
import { getCountrySchedule } from '@/api/home'
export default {
  name: 'TeamSchedule',
  components: {
    VPaging,
  },
  data() {
    return {
      pageIndex: 1,
      schedules: [],
      amount: 0,
    }
  },
  mounted() {
    console.log('TeamSchedule-mounted')
    this.initData()
  },
  methods: {
    chagePage(val) {
      this.pageIndex = val
      this.initData()
    },
    async initData() {
      const countryID = this.$route.params.id
      const res = await getCountrySchedule({ CountryID: countryID, Page: this.pageIndex })
      this.schedules = res.data.Data.CountryScheduleList
      this.amount = res.data.Data.SumCount
    },
  },
}
</script>

<style lang="scss">
.team-schedule {
  padding: 10px;
  background-color: $white;
  table {
    width: 100%;
    font-size: $fs-12;
  }
  th,
  td {
    height: 44px;
    vertical-align: middle;
    text-align: center;
  }
  th {
    background-color: $secondary-color;
    color: $text-color-scd;
  }
  tr:nth-child(2n) {
    background-color: $secondary-color;
  }
  a {
    color: $text-color;
    text-decoration: none;
    padding: 0 3px;
  }
  .emphasis {
    color: #de1e30;
  }
}
</style>
