<template>
  <div class="stadium">
    <v-header :navIndex="4"></v-header>
    <v-banner></v-banner>
    <main class="w-1200">
      <div class="stadium-tab-container">
        <div class="stadium-tab">
          <div class="tab-item" @click="active(1)" :class="{ active_item1: index == 1 }">
            <p :class="{ active_p: index == 1 }">全部场馆</p>
          </div>
          <div class="tab-item" @click="active(2)" :class="{ active_item2: index == 2 }">
            <p :class="{ active_p: index == 2 }">场馆距离</p>
          </div>
        </div>

        <div class="container" v-show="index == 1" v-if="list">
          <div class="stadium-name">
            <div
              v-for="(item, stadiumIndex) of list"
              :key="stadiumIndex"
              @click="stadiumActive(stadiumIndex)"
              :class="{ stadiumActiveStyle: stadiumIndex == newIndex }"
            >
              <p :class="{ stadiumActiveStyle1: stadiumIndex == newIndex }">{{ item.Venue.Name }}</p>
              <p :class="{ stadiumActiveStyle1: stadiumIndex == newIndex }">({{ item.Venue.EnglishName }})</p>
              <p :class="{ stadiumActiveStyle1: stadiumIndex == newIndex }">
                <img :src="item.Venue.CountryImg" />
                <span>{{ item.Venue.CountryName }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>

      <!-- 场馆介绍和场馆比赛信息 ---如果stadiums异步获取到了数据,则渲染-->
      <div class="stadium-content" v-show="index == 1" v-if="stadiums">
        <div class="content-left">
          <p><img :src="stadiums.Venue.Img" /></p>
          <p>{{ stadiums.Venue.Name }}</p>
          <p>{{ stadiums.Venue.EnglishName }}</p>
          <p>{{ stadiums.Venue.Capacity }}</p>
          <p>{{ stadiums.Venue.City }}</p>
          <p>
            {{ stadiums.Venue.Introduction }}
          </p>
        </div>
        <div class="content-right">
          <div class="content-right-title">
            <p>本届赛事承办的比赛</p>
            <ul class="content-right-list">
              <li class="right-list-item" v-for="(item, index) of stadiums.Schedule" :key="index">
                <p class="right-time">{{ item.Time }}</p>
                <p class="right-date">{{ item.Month }}-{{ item.Day }}</p>
                <p class="right-state">{{ item.Result }}</p>
                <p class="right-team-info">
                  <span>{{ item.AName }}</span>
                  <img :src="item.AImg1" />
                  <!-- <span>20</span> --模拟有比分时布局样式-->
                  <span>-</span>
                  <!-- <span>8</span> ---模拟有比分时布局样式-->
                  <img :src="item.BImg1" />
                  <span>{{ item.BName }}</span>
                </p>
                <p class="right-active-icon">feiji</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!-- ---------------------地图------------------------- -->
      <div class="container" v-show="index == 2">
        <div class="w-1200">
          <v-map width="1200" height="700"></v-map>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { postStadium } from '@/api/news.js'
import VBanner from '@/components/v-banner.vue'
import VHeader from '../components/v-header.vue'
import VMap from '@/components/v-map'
export default {
  name: 'Stadium',
  components: {
    VBanner,
    VHeader,
    VMap,
  },
  data() {
    return {
      index: '1',
      newIndex: '0',
      list: null, //页面数据
      stadiums: null,
    }
  },
  async created() {
    //异步获取页面数据
    const res = await postStadium()
    this.list = res.data.Data.List
    this.stadiums = this.list[0]
  },
  methods: {
    active(index) {
      //tab切换方法
      this.index = index
    },
    stadiumActive(index) {
      //stadium的tab切换方法
      this.newIndex = index
      //更新data
      this.stadiums = this.list[index]
    },
  },
}
</script>

<style lang="scss">
.stadium {
  //----------------------------
  @mixin flex-ceneter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .active_item1 {
    border-top: 40px solid $primary-color !important;
  }
  .active_item2 {
    background: $primary-color !important;
  }
  .active_p {
    color: $white !important;
  }
  .stadiumActiveStyle {
    background: $primary-color !important;
  }
  .stadiumActiveStyle1 {
    color: white !important;
  }
  //----------------------------

  .stadium-tab-container {
    width: 100%;
    padding: 12px;
    background: $white;
    position: relative;
    box-sizing: border-box;
    margin-bottom: 10px;
  }

  .stadium-tab {
    width: 100%;
    height: 40px;
    display: flex;
    background: $white;
  }
  .tab-item {
    cursor: pointer;
    transition: all 0.3s ease;
  }
  .tab-item > p {
    color: $white;
    color: #999;
    width: 160px;
    height: 40px;
    font-size: $fs-16;
    line-height: 40px;
    text-align: center;
    transition: all 0.3s ease;
    pointer-events: none;
  }
  .tab-item:nth-child(1):hover {
    border-top: 40px solid$primary-color;
  }
  .tab-item:nth-child(2):hover {
    background: $primary-color;
  }

  .tab-item:nth-child(1):hover > p,
  .tab-item:nth-child(2):hover > p {
    color: $white;
  }

  .tab-item:nth-child(1) {
    width: 150px;
    border-top: 40px solid #f7f7f8;
    border-right: 24px solid transparent;
    position: relative;
  }
  .tab-item:nth-child(1) > p {
    position: absolute;
    top: -40px;
  }

  .tab-item:nth-child(2) {
    width: 150px;
    height: 40px;
    background: #f7f7f8;
    position: relative;
    transform: rotateZ(0deg) skew(-30deg, 0deg);
  }
  .tab-item:nth-child(2) > p {
    transform: rotateZ(0deg) skew(30deg, 0deg);
  }

  //-----------------------------------------------
  .container {
    width: 100%;
  }
  .stadium-name {
    width: 100%;
    height: 216px;
    display: flex;
    justify-content: space-around;
    align-content: space-around;
    flex-wrap: wrap;
    margin-top: 20px;
  }
  .stadium-name > div {
    width: 186px;
    height: 100px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    background: #f7f7f8;
    transition: all 0.5s ease;
    cursor: pointer;
    padding: 10px;
    box-sizing: border-box;
  }

  .stadium-name > div > p:nth-child(1) {
    font-size: $fs-16;
    color: $black;
  }
  .stadium-name > div > p:nth-child(3) {
    @include flex-ceneter;
  }
  .stadium-name > div > p:nth-child(3) > img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 5px;
  }

  .stadium-name > div:hover {
    background: $primary-color;
  }
  .stadium-name > div:hover > p {
    color: $white;
  }

  .stadium-name > div > p {
    width: 100%;
    text-align: center;
    margin: 0 auto;
    transition: all 0.3s ease;
  }

  //------------------------------------------
  .stadium-content {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .content-left {
    width: 390px;
    height: 771px;
    background: linear-gradient(135deg, transparent 0px, $white 0) top left,
      linear-gradient(-135deg, transparent 15px, $white 0) top right, linear-gradient(-45deg, transparent 0px, $white 0) bottom right,
      linear-gradient(45deg, transparent 15px, $white 0) bottom left;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    padding: 20px;
    box-sizing: border-box;
  }
  .content-left > p {
    margin-top: 20px;
    font-size: $fs-16;
  }
  .content-left > p:nth-child(1) {
    margin-top: 0px;
    text-align: center;
  }
  .content-left > p:nth-child(1) > img {
    width: 350px;
    height: 195px;
  }

  .content-left > p:nth-child(2),
  .content-left > p:nth-child(3) {
    font-size: $fs-20 !important;
    font-weight: 500 !important;
  }

  .content-left > p:nth-child(6) {
    line-height: 25px;
  }
  //--------content-right

  .content-right {
    width: 800px;
    height: 771px;
    background: linear-gradient(135deg, transparent 0px, $white 0) top left,
      linear-gradient(-135deg, transparent 15px, $white 0) top right, linear-gradient(-45deg, transparent 0px, $white 0) bottom right,
      linear-gradient(45deg, transparent 15px, $white 0) bottom left;
    background-size: 50% 50%;
    background-repeat: no-repeat;
  }

  .content-right-title {
    width: 200px;
    border-top: 40px solid $primary-color;
    border-right: 40px solid transparent;
    position: relative;
  }
  .content-right-title > p {
    width: 200px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: $fs-16;
    color: $white;
    position: absolute;
    top: -40px;
  }
  .content-right-list {
    width: 800px;
    padding: 10px;
    box-sizing: border-box;
  }
  .right-list-item {
    width: 100%;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: $fs-16;
    font-weight: 600;
    background: #f7f7f8;
    margin-bottom: 10px;
    transition: all 0.4s ease;
  }
  .right-list-item:hover {
    background: #c2f3fa;
  }

  .right-time {
    width: 70px;
    border-right: 1px solid rgb(204, 203, 203);
  }
  .right-team-info {
    width: 366px;
    height: 100%;
    @include flex-ceneter;
    justify-content: space-around;
  }
  .right-team-info > img {
    border-radius: 50%;
    width: 30px;
    height: 30px;
  }
  .right-team-info :first-child {
    display: inline-block;
    width: 100px;
    height: 40px;
    margin-right: -13px;
    line-height: 40px;
    text-align: end;
  }
  .right-team-info :last-child {
    display: inline-block;
    width: 100px;
    height: 40px;
    margin-left: -13px;
    line-height: 40px;
  }
}
</style>
