<template>
  <div class="history">
    <v-header :navIndex="6"></v-header>
    <v-banner></v-banner>
    <main class="w-1200 main-strange">
      <div class="content-box">
        <!-- TAB控制器 -->
        <div class="history-title" @click="setListOfShow(0, $event)">
          <p>欧洲足球锦标赛夺冠次数统计</p>
          <span class="iconfont" :class="{ jiantou: inedx == 0 }">&#xe6b5;</span>
        </div>

        <!-- --------------------欧洲足球锦标赛夺冠次数统计--------------- -->
        <transition name="fade">
          <ul class="win-content" v-if="res" v-show="inedx == 0">
            <li class="win-list-title">
              <p>名次</p>
              <p>国家</p>
              <p>获奖次数</p>
              <p>获奖年份</p>
            </li>
            <li class="win-list-item" v-for="(item, index) of WinCount" :key="index">
              <p>第{{ item.Rank }}名</p>
              <div>
                <img :src="item.Img" class="flag-icon" />
                <span>{{ item.Name }}</span>
              </div>
              <p>
                <span>{{ item.Count }}</span>
                <img src="~@/assets/images/pic/1.png" class="trophy-icon" />
              </p>
              <p>{{ item.Year }}</p>
            </li>
          </ul>
        </transition>
      </div>
      <!-- --------------------历届决赛对阵-------------- -->
      <div class="content-box" v-if="res">
        <div class="history-title" @click="setListOfShow(1, $event)">
          <p>历届决赛对阵</p>
          <span class="iconfont" :class="{ jiantou: inedx == 1 }">&#xe6b5;</span>
        </div>
        <transition name="fade">
          <ul class="final-content" v-show="inedx == 1">
            <li class="final-list-item" v-for="(item, index) of PreviousFinals" :key="index">
              <div class="item-top"><img :src="item.Img" /></div>
              <div class="item-bottom">
                <p>
                  <span>{{ item.ATeam }}</span>
                  <img :src="item.AImg" class="flag-icon" />
                </p>
                <p>{{ item.Anum }}:{{ item.Bnum }}</p>
                <p>
                  <img :src="item.BImg" class="flag-icon" />
                  <span>{{ item.BTeam }}</span>
                </p>
              </div>
            </li>
          </ul>
        </transition>
      </div>
      <!-- ---------------历届冠军------------------- -->
      <div class="content-box" v-if="res">
        <div class="history-title" @click="setListOfShow(2, $event)">
          <p>历届冠军</p>
          <span class="iconfont" :class="{ jiantou: inedx == 2 }">&#xe6b5;</span>
        </div>
        <transition name="fade">
          <ul class="champion-content" v-show="inedx == 2">
            <li class="champion-list-item" v-for="(item, index) of PreviousChampion" :key="index">
              <div class="champion-top"><img :src="item.Img" /></div>
              <div class="champion-bottom">
                <p>
                  <img :src="item.AImg" class="flag-icon" />
                </p>
                <p>
                  {{ item.ATeam }}
                </p>
              </div>
            </li>
          </ul>
        </transition>
      </div>
      <!-- ------------------历史射手榜------------ -->
      <div class="content-box" v-if="res">
        <div class="history-title" @click="setListOfShow(3, $event)">
          <p>历史射手榜</p>
          <span class="iconfont" :class="{ jiantou: inedx == 3 }">&#xe6b5;</span>
        </div>
        <transition name="fade">
          <ul class="Shooter-content" v-show="inedx == 3">
            <li class="Shooter-list-title">
              <p>名次</p>
              <p>总进球数</p>
              <p>球员</p>
              <p>所属国家</p>
              <p>场上位置</p>
              <p>最佳射手次数</p>
            </li>
            <li class="Shooter-list-item" v-for="(item, index) of TopScorer" :key="index">
              <p>{{ item.Nnm }}</p>
              <p>{{ item.Goal }}球</p>
              <p>{{ item.Name }}</p>
              <p>
                <img :src="item.CountryImg" class="flag-icon" />
                <span>{{ item.Country }}</span>
              </p>
              <p>
                {{ item.Position }}
              </p>
              <p>{{ item.Ace }}</p>
            </li>
          </ul>
        </transition>
      </div>
      <!-- ------------------历届欧洲杯射手榜----------------- -->
      <div class="content-box" v-if="res">
        <div class="history-title" @click="setListOfShow(4, $event)">
          <p>历届欧洲杯射手榜</p>
          <span class="iconfont" :class="{ jiantou: inedx == 4 }">&#xe6b5;</span>
        </div>
        <transition name="fade">
          <ul class="ShooterOld-content" v-show="inedx == 4">
            <li class="ShooterOld-list-title">
              <p>年份</p>
              <p>进球数</p>
              <p>最佳射手</p>
              <p>冠军</p>
              <p>亚军</p>
              <p>举办地</p>
            </li>
            <li class="ShooterOld-list-item" v-for="(item, index) of PreviousShooter" :key="index">
              <p>{{ item.Year }}</p>
              <p>{{ item.Goal }}</p>
              <p class="ShooterOld-name">{{ item.Ace }}</p>
              <p>{{ item.ATeam }}</p>
              <p>{{ item.BTeam }}</p>
              <p>{{ item.HostCountry }}</p>
            </li>
          </ul>
        </transition>
      </div>
      <!-- --------------------历史最佳球员--------------- -->
      <div class="content-box">
        <div class="history-title" @click="setListOfShow(5, $event)">
          <p>历史最佳球员</p>
          <span class="iconfont" :class="{ jiantou: inedx == 5 }">&#xe6b5;</span>
        </div>
        <transition name="fade">
          <ul class="ShooterOld-content" v-show="inedx == 5">
            <li class="ShooterOld-list-title">
              <p>届数</p>
              <p>年份</p>
              <p>最佳球员</p>
              <p>所属国家</p>
              <p>场上位置</p>
              <p>球队成绩</p>
            </li>
            <li class="ShooterOld-list-item" v-for="(item, index) of PreviousPlayer" :key="index">
              <p>{{ index + 1 }}</p>
              <p>{{ item.Year }}</p>
              <p>{{ item.Optimal }}</p>
              <p class="player">
                <img :src="item.CountryOriginImg" class="flag-icon" />
                <span>{{ item.CountryOrigin }}</span>
              </p>
              <p>{{ item.PlayerPosition }}</p>
              <p>{{ item.CountryOriginGrades }}</p>
            </li>
          </ul>
        </transition>
      </div>
    </main>
  </div>
</template>

<script>
import { postHistory } from '@/api/news.js'
import VBanner from '@/components/v-banner.vue'
import VHeader from '../components/v-header.vue'
export default {
  name: 'History',
  components: {
    VBanner,
    VHeader,
  },
  data() {
    return {
      inedx: 0, //控制内容列表显示的变量
      res: null,
      WinCount: null, //夺冠次数
      PreviousFinals: null, //决赛对阵
      PreviousChampion: null, //历届冠军
      TopScorer: null, //历史射手榜
      PreviousShooter: null, //历届欧洲杯射手榜
      PreviousPlayer: null, //历史最佳球员
    }
  },
  async created() {
    //异步获取数据
    this.res = await postHistory()
    this.WinCount = this.res.data.Data.WinCount
  },
  methods: {
    setListOfShow(index, event) {
      //控制内容列表显示的方法
      this.inedx = index

      switch (index) {
        case 0: //夺冠次数
          this.WinCount = this.res.data.Data.WinCount
          break
        case 1: //历届对阵
          this.PreviousFinals = this.res.data.Data.PreviousFinals
          break
        case 2: //历届冠军
          this.PreviousChampion = this.res.data.Data.PreviousChampion
          break
        case 3: //历史射手榜
          this.TopScorer = this.res.data.Data.TopScorer
          break
        case 4: //历届欧洲杯射手榜
          this.PreviousShooter = this.res.data.Data.PreviousShooter
          break
        case 5: //历史最佳球员
          this.PreviousPlayer = this.res.data.Data.PreviousPlayer
          break
      }
    },
  },
}
</script>

<style lang="scss">
.history {
  //----------------------------------------------
  @mixin flex-ceneter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .main-strange {
    padding: 12px 20px 20px;
    position: relative;
    background: linear-gradient(135deg, transparent 0px, $white 0) top left,
      linear-gradient(-135deg, transparent 15px, $white 0) top right, linear-gradient(-45deg, transparent 0px, $white 0) bottom right,
      linear-gradient(45deg, transparent 15px, $white 0) bottom left;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    box-sizing: border-box;
  }
  .fade-enter-active {
    transition: opacity 0.6s ease-in-out;
  }
  .fade-enter {
    opacity: 0;
  }
  .jiantou {
    transform: rotateZ(180deg);
  }
  //---------------------------------------------
  .content-box {
    border-top: 1px solid $br-color-base;
    border-bottom: 1px solid $br-color-base;
  }
  .content-box:nth-child(1) {
    border-top: none;
  }
  .history-title {
    display: flex;
    align-items: center;
    height: 65px;
    padding-left: 13px;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;
  }
  .history-title::after {
    content: '';
    position: absolute;
    width: 3px;
    height: 12px;
    background: $primary-color;
    border-radius: 1px;
    left: 0;
    top: 26px;
  }

  .history-title > p {
    width: 1160px;
    height: 65px;
    line-height: 65px;
    font-size: $fs-18;
    font-weight: 600;
    color: $black;
  }

  // ------------------------------------夺冠次数样式
  .win-content > li {
    width: 100%;
    height: 65px;
    @include flex-ceneter;
  }
  .win-content > li > p {
    flex: 1;
    @include flex-ceneter;
  }
  .win-list-title {
    font-size: $fs-16;
    font-weight: 600;
    background: #f7f8fa;
  }

  .win-list-item {
    border-bottom: 1px solid $br-color-base;
    background: #fcfcfd;
  }

  .win-list-item > p:nth-child(1),
  .win-list-item > p:nth-child(3),
  .win-list-item > p:nth-child(4) {
    font-size: $fs-18;
    font-weight: 600;
  }
  .win-list-item > div:nth-child(2) {
    display: flex;
    align-content: center;
    width: 292px;
    height: 30px;
    font-weight: 600;
    padding-left: 85px;
    box-sizing: border-box;
    line-height: 30px;
  }

  .win-content :last-child {
    border-bottom: none;
  }
  .flag-icon,
  .trophy-icon {
    margin: 0 10px;
  }
  .flag-icon {
    border-radius: 50%;
    width: 30px;
    height: 30px;
  }
  .trophy-icon {
    width: 20px;
    height: 36px;
  }

  // ------------------------------------历届决战对阵样式
  .final-content {
    display: flex;
    flex-wrap: wrap;
  }
  .final-list-item {
    background: #f7f7f8;
    width: 273px;
    height: 150px;
    margin: 5px;
    padding: 10px 0;
    box-sizing: border-box;
  }
  .item-top {
    width: 100%;
    text-align: center;
  }
  .item-top > img {
    width: 110px;
    height: 55px;
  }
  .item-bottom {
    width: 100%;
    margin-top: 20px;
    @include flex-ceneter;
  }
  .item-bottom > p {
    font-size: $fs-16;
    height: 50px;
    line-height: 50px;
    @include flex-ceneter;
  }

  // ------------------------------------历届冠军
  .champion-content {
    display: flex;
    flex-wrap: wrap;
  }
  .champion-list-item {
    background: #f7f7f8;
    width: 132px;
    height: 147px;
    margin: 5px;
    padding: 10px 0;
    box-sizing: border-box;
  }
  .champion-top {
    width: 100%;
    text-align: center;
  }
  .champion-top > img {
    width: 110px;
    height: 56px;
  }

  .champion-bottom {
    width: 100%;
  }
  .champion-bottom > p {
    font-size: $fs-18;
    line-height: 50px;
    @include flex-ceneter;
  }

  // ------------------------------------历史射手榜

  .Shooter-list-title {
    width: 1160px;
    font-size: $fs-18;
    font-weight: 600;
    background: #f7f8fa;
    display: flex;
  }
  .Shooter-list-item > p,
  .Shooter-list-title > p {
    font-size: $fs-16;
    font-weight: 600;
    height: 63px;
    flex: 1;
    text-align: center;
    @include flex-ceneter;
  }
  .Shooter-list-item > p:nth-child(1) {
    font-size: $fs-18;
    font-weight: 600;
  }
  .Shooter-list-item > p:nth-child(4) {
    justify-content: flex-start;
  }
  .Shooter-list-item > p:nth-child(4) > img {
    margin-left: 55px;
  }

  .Shooter-list-item {
    display: flex;
    border-bottom: 1px solid $br-color-base;
  }
  .Shooter-list-item:last-child {
    border-bottom: none;
  }

  // ------------------------------------历届欧洲杯射手榜和历史最佳球员样式重叠

  .ShooterOld-list-title {
    width: 1160px;
    font-size: $fs-18;
    font-weight: 600;
    background: #f7f8fa;
    display: flex;
  }
  .ShooterOld-list-item > p,
  .ShooterOld-list-title > p {
    font-size: $fs-16;
    font-weight: 600;
    height: 63px;
    flex: 1;
    text-align: center;
    @include flex-ceneter;
  }
  .ShooterOld-name {
    line-height: 22px;
    font-size: 15px !important;
  }

  .ShooterOld-list-item {
    display: flex;
    border-bottom: 1px solid $br-color-base;
  }
  .ShooterOld-list-item:last-child {
    border-bottom: none;
  }

  // ------------------------------------历史最佳球员
  .player-list-title {
    width: 1160px;
    font-size: $fs-18;
    font-weight: 600;
    background: #f7f8fa;
    display: flex;
  }
  .player-list-item > p,
  .player-list-title > p {
    font-size: $fs-16;
    font-weight: 600;
    height: 63px;
    flex: 1;
    text-align: center;
    @include flex-ceneter;
  }
  .player-list-item > p:nth-child(1) {
    font-size: $fs-18;
    font-weight: 600;
  }

  .player-list-item:last-child {
    border-bottom: none;
  }

  .player {
    justify-content: flex-start !important;
  }

  .player > img {
    margin-left: 55px;
  }
}
</style>
