import service from '@/utils/service'

// api接口定义规范 👇
/**
 * @desc get请求测试
 * @param  {Object} params 请求参数
 * @return {Object|Promise} 响应结果（一个Promise）
 */
export function getReqTest(params) {
  return service.request({
    url: `/facts`,
    method: 'GET',
    params,
  })
}
/**
 * @desc post请求测试
 * @param  {Object} data 请求参数
 * @return {Object|Promise} 响应结果（一个Promise）
 */
export function postReqTest(data) {
  return service.request({
    url: `/api/postTest`,
    method: 'POST',
    data,
  })
}

