<template>
  <div class="schedule-carousel">
    <div class="carousel-container">
      <div class="swiper-button-prev"></div>
      <swiper ref="cpSwiper" :options="swiperOptions" class="swiper-container">
        <swiper-slide class="swiper-slide" v-for="item of hotSchedules" :key="item.ID">
          <div class="competition" @click="swiperClick(item.ID)">
            <p>{{ item.Month }}月{{ item.Day }}日 {{ item.Time }}</p>
            <p class="type">{{ item.Remarks }}</p>
            <p class="team-icon">
              <img :src="item.AImg1" :alt="item.AName" />
              <span>-:-</span>
              <img :src="item.BImg1" :alt="item.BName" />
            </p>
            <p class="team-name">
              <span>{{ item.AName }}</span>
              <a>关注</a>
              <span>{{ item.BName }}</span>
            </p>
          </div>
        </swiper-slide>
      </swiper>
      <div class="swiper-button-next"></div>
      <div class="all-button">
        <router-link to="/schedule" class="router-link--reset">
          <span class="iconfont icon-zuqiuchang"></span>
          <h3>全部赛程</h3>
        </router-link>
      </div>
    </div>
    <div class="video-container">
      <div class="title-tabs">
        <div class="tab-item">
          <p>{{ isLive ? '正在直播' : '经典赛事轮播' }}</p>
        </div>
        <div class="tab-item">
          <p>{{ liveName }}</p>
        </div>
      </div>
      <v-video-room v-if="videoCtIsShow" :url="liveUrl" :isLive="isLive"></v-video-room>
      <div class="collapse-wrapper">
        <div class="collapse" @click="collapseClick">
          <i class="triangle" :class="{ 'triangle--down': !videoCtIsShow }"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import VVideoRoom from '@/components/v-video-room'
import { getHotSchedule } from '@/api/home'
export default {
  name: 'schedule-carousel',
  components: {
    Swiper,
    SwiperSlide,
    VVideoRoom,
  },
  data() {
    return {
      swiperOptions: {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        // Some Swiper option/callback...
        slidesPerView: 4,
        spaceBetween: 15,
      },
      videoCtIsShow: true,
      hotSchedules: [],
      liveUrl: '',
      liveName: '',
      isLive: false,
    }
  },
  created() {
    this.initHotSchedule()
  },
  beforeDestroy() {
    const cpSwiper = this.$refs.cpSwiper.$swiper
    cpSwiper.destroy()
  },
  methods: {
    async initHotSchedule() {
      const res = await getHotSchedule()
      const { ScheduleList, liveName, liveUrl, IsLive } = res.data.Data
      this.hotSchedules = ScheduleList
      this.liveName = liveName
      this.liveUrl = liveUrl
      this.$store.commit('setHotSchedules', res.data.Data.ScheduleList)
      this.isLive = IsLive
    },
    swiperClick(idx) {
      // this.videoCtIsShow = true
      this.$router.push('/watch/' + idx)
    },
    collapseClick() {
      if (this.videoCtIsShow) {
        this.videoCtIsShow = false
      } else {
        this.videoCtIsShow = true
      }
    },
  },
}
</script>

<style lang="scss">
.schedule-carousel {
  .carousel-container {
    position: relative;
    padding: 20px;
    background-color: $white;
    border-radius: 5px;
  }
  --swiper-navigation-color: #aaa; /* 单独设置按钮颜色 */
  --swiper-navigation-size: 24px; /* 设置按钮大小 */
  .swiper-button-prev,
  .swiper-button-next {
    width: 28px;
    position: absolute;
    height: 100%;
    top: 0 !important;
    background-color: $br-color-base;
    margin: 0;
    border-radius: 5px;
    outline: none;
  }
  .swiper-button-prev {
    left: -36px !important;
  }
  .swiper-button-next {
    right: -36px !important;
  }
  .all-button {
    width: 50px;
    height: 145px;
    margin-top: 20px;
    position: absolute;
    right: 20px;
    top: 0;
    z-index: 999;
    background-color: $br-color-base;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    span {
      font-size: $fs-22;
      color: #aaa69d;
    }
    h3 {
      width: 30px;
      font-size: $fs-base;
      margin-top: 6px;
    }
  }
  .swiper-slide {
    width: 266px !important;
    height: 145px;
    border: 1px solid $br-color-base;
    border-radius: 5px;
    box-sizing: border-box;
    margin-right: 12px !important;
    cursor: pointer;
  }
  .competition {
    color: $h-color;
    p {
      text-align: center;
      padding: 8px;
    }
    .type {
      font-size: $fs-12;
      color: $text-color;
    }
    .team-icon {
      img {
        width: 30px;
        height: 30px;
        vertical-align: middle;
      }
      span {
        margin: 0 30px;
      }
    }
    .team-name {
      a {
        margin: 0 30px;
        display: inline-block;
        width: 36px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        background-color: #3fbdd0;
        color: $white;
        border-radius: 3px;
        font-size: $fs-12;
      }
    }
  }
  .video-container {
    width: 100%;
    background-color: $white;
    margin-top: 10px;
    border-radius: 3px;
    padding: 20px 20px 40px;
    position: relative;
    box-sizing: border-box;
    .title-tabs {
      display: flex;
      color: $white;
      font-size: $fs-16;
    }
    .tab-item {
      min-width: 170px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      background-color: $primary-color;
      position: relative;
      &:nth-child(1)::after {
        content: '';
        border-width: 40px 23px;
        border-color: $primary-color transparent transparent transparent;
        border-style: solid;
        position: absolute;
        right: -23px;
        top: 0;
      }
      &:nth-child(2) {
        margin-left: 20px;
        transform: skewX(-30deg);
        font-size: $fs-base;
        padding: 0 15px;
        p {
          transform: skewX(30deg);
        }
      }
    }
    .collapse-wrapper {
      width: 98px;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      overflow: hidden;
    }
    .collapse {
      width: 70px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      background-color: $primary-color;
      position: relative;
      margin: 0 auto;
      cursor: pointer;
      &::before {
        content: '';
        border-width: 20px 14px;
        border-color: transparent $primary-color transparent transparent;
        border-style: solid;
        position: absolute;
        left: -28px;
        top: 0;
      }
      &::after {
        content: '';
        border-width: 20px 14px;
        border-color: transparent transparent transparent $primary-color;
        border-style: solid;
        position: absolute;
        right: -28px;
        top: 0;
      }
      .triangle {
        display: inline-block;
        border: 6px solid;
        border-color: transparent transparent $white transparent;
        &--down {
          border-color: $white transparent transparent transparent;
        }
      }
    }
  }
}
</style>
