<template>
  <div class="news-details">
    <div class="news-details-container">
      <h1 class="title">{{ newsData.Title }}</h1>
      <div class="text-info">
        <p>发布时间:{{ newsData.Time && newsData.Time.split(' ')[0] }}</p>
        <p>编辑:环球体育</p>
      </div>
      <!-- 文章内容主题 -->
      <div class="content" v-html="newsData.Text"></div>
      <!-- ---------- -->
      <div class="news-details-icon">
        <p>
          <span>环球体育</span>
          <span>欧洲杯</span>
          <span>资讯</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { getNesDetails } from '@/api/news.js'
export default {
  name: 'NewsDetails',
  data() {
    return {
      newsId: '',
      newsData: {},
    }
  },
  async mounted() {
    this.newsId = this.$route.params.id
    this.initNewsDetails()
    this.clearRemnant()
  },
  methods: {
    async initNewsDetails() {
      const res = await getNesDetails({ ID: this.newsId })
      this.newsData = res.data.Data.AdvisoryList[0]
    },
    clearRemnant() {
      //清除爬取的文章当中可能存在的A标签外链
      let a = document.querySelectorAll('.news-details  .content a')
      Array.from(a).forEach(item => {
        item.href = ''
      })
    },
  },
}
</script>

<style lang="scss">
.news-details {
  width: 900px;
  margin: 0 auto;
  position: relative;
  .news-details-container {
    width: 900px;
    min-height: 800px;
    background: $white;
    padding: 20px 0;

    .title {
      width: 700px;
      font-size: 26px;
      font-weight: 600;
      color: $black;
      text-align: center;
      margin: 0 auto;
      line-height: 30px;
      margin-bottom: 20px;
    }

    .text-info {
      width: 400px;
      font-size: 14px;
      font-weight: 10px;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
    }

    //---------------------
    .content {
      width: 800px;
      margin: 20px auto;
      border-top: 1px solid rgb(192, 192, 192);
    }

    .content img {
      display: block;
      margin: 0 auto;
      max-width: 100%;
    }
    .content p {
      font-size: 18px;
      margin: 10px;
      line-height: 25px;
      text-indent: 25px;
    }
    .news-details-icon {
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 0 70px;
    }
    .news-details-icon > p > span {
      font-size: $fs-15;
      padding: 2px;
      margin: 0 10px;
      background: $primary-color;
      color: $white;
    }
    .news-details-test {
      position: relative;
    }
    .news-details-test::before {
      content: attr(data-length);
      position: absolute;
      font-size: 13px;
      right: -18px;
      color: rgb(158, 158, 158);
    }
  }
  //-------------------------------评论
  .comment {
    margin: 10px auto;
    width: 900px;
    height: 454px;
    background: $white;
    position: relative;
  }
  .notHasData {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 456px;
    font-size: $fs-16;
    color: rgb(197, 195, 195);
  }
  .comment-title {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    font-size: $fs-18;
    font-weight: 600;
    color: $black;
    box-sizing: border-box;
    padding: 10px;
    padding-left: 50px;
    border-bottom: 1px solid rgba(219, 219, 219, 0.5);
  }
  .comment-title > p {
    padding-left: 5px;
    border-left: 3px solid #e74c5b;
  }
  .comment-content {
    width: 100%;
    display: flex;
    box-sizing: border-box;
    padding: 0 50px;
    border-bottom: 1px solid rgba(214, 214, 214, 0.5);
  }
  .comment-content-left {
    height: 100px;
    flex: 1;
  }
  .comment-content-left > img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-left: 20px;
    margin-top: 10px;
  }
  .comment-content-right {
    flex: 9;
    height: 100px;
  }
  .right-box-top {
    width: 100%;
    height: 70px;
    font-size: $fs-15;
    box-sizing: border-box;
    padding-top: 10px;
  }
  .right-box-top > p:nth-child(1) {
    font-weight: 600;
    margin-bottom: 10px;
  }
  .right-box-top > p:nth-child(2) {
    word-break: break-all;
  }
  .right-box-bottom {
    width: 100%;
    margin-top: 5px;
  }
  //----------------emoji-----------------
  .comment-input {
    margin: 0 auto;
    margin-top: 10px;
    width: 900px;
    height: 150px;
    padding: 0 50px;
    box-sizing: border-box;
    background: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  .inputText {
    width: 600px;
    height: 70px;
    outline: none;
    resize: none;
    font-size: $fs-15;
  }
  .comment-btn {
    margin-left: 30px;
    width: 80px;
    height: 40px;
    outline: none;
    cursor: pointer;
  }
  .emoji-btn {
    position: absolute;
    top: 10px;
    left: 90px;
    display: inline-block;
    font-size: $fs-20;
    width: 30px;
    cursor: pointer;
  }
  .emoji-box {
    position: absolute;
    width: 438px;
    border: 1px solid rgb(221, 218, 218);
    z-index: 2;
    top: -218px;
    left: 124px;
    display: flex;
    padding: 2px;
    box-sizing: border-box;
    align-items: center;
    flex-wrap: wrap;
    background: $white;
    box-shadow: 0 0 10px 1px #cfcece;
    cursor: pointer;
  }
  .emoji-box > span {
    width: 27px;
    height: 27px;
    font-size: $fs-20;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  // -------------------提示框-------------------------
  .prompt-box {
    width: 200px;
    height: 60px;
    background: rgba(34, 188, 235, 0.6);
    position: fixed;
    top: 60%;
    left: 50%;
    margin-top: -30px;
    margin-left: -100px;
    text-align: center;
    line-height: 60px;
    font-size: $fs-18;
    color: $white;
    box-shadow: 0 0 6px 2px rgb(34, 188, 235);
    transition: all 0.6s ease;
    opacity: 0;
    border-radius: 10px;
    z-index: -999;
  }
  .promptBoxStyle {
    top: 40%;
    opacity: 1;
    z-index: 999;
  }
}
</style>
