<template>
  <div class="v-desc">
    <div class="desc-left">
      <img :src="imgUrl" alt="" />
      <div class="info">
        <h1 class="zh-name">
          <span
            class="text-ani--comedown"
            :style="{ 'animation-delay': index / 10 + 's' }"
            v-for="(item, index) of nameZhAniArr"
            :key="index"
            >{{ item }}</span
          >
        </h1>
        <p class="en-name">
          <span
            class="text-ani--comeup"
            :style="{ 'animation-delay': index / 10 + 's' }"
            v-for="(item, index) of nameENAniArr"
            :key="index"
            >{{ item }}</span
          >
        </p>
      </div>
    </div>
    <div class="desc-right">
      <div class="overfl-wrap">
        <div class="status">
          <p class="text">
            <span
              class="text-ani--comeup"
              :style="{ 'animation-delay': index / 10 + 's' }"
              v-for="(item, index) of statusAniArr"
              :key="index"
              >{{ item }}</span
            >
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'v-desc',
  props: {
    zhName: String,
    enName: String,
    status: String,
    imgUrl: String,
  },
  computed: {
    statusAniArr() {
      return this.status.split('')
    },
    nameZhAniArr() {
      return this.zhName.split('')
    },
    nameENAniArr() {
      return this.enName.split('')
    },
  },
}
</script>

<style lang="scss">
.v-desc {
  height: 72px;
  display: flex;
  justify-content: space-between;
  background: url(~@/assets/images/pic/banner-2.png) no-repeat center;
  background-size: cover;
  background-color: $white;
  .text-ani--comeup {
    display: inline-block;
    animation: comeUp 0.6s ease;
  }
  .text-ani--comedown {
    display: inline-block;
    animation: comeDown 0.6s ease;
  }
  @keyframes comeUp {
    from {
      transform: translate(-20px, 100px);
    }
    to {
      transform: translate(0, 0);
    }
  }
  @keyframes comeDown {
    from {
      transform: translate(0, -80px);
    }
    to {
      transform: translate(0, 0);
    }
  }
  @keyframes toLeftGrow {
    to {
      transform: translateX(0);
    }
  }
  .desc-left {
    display: flex;
    align-items: center;
    padding-left: 15px;
    overflow: hidden;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    .info {
      margin-left: 15px;
      .zh-name {
        font-size: $fs-20;
        color: $h-color;
      }
      .en-name {
        font-size: $fs-12;
        color: $text-color-scd;
        margin-top: 8px;
      }
    }
  }
  .desc-right {
    overflow: hidden;
    .overfl-wrap {
      width: 160px;
      height: 0;
      border-width: 0 0 72px 72px;
      border-style: solid;
      border-color: transparent transparent #dfe4ea transparent;
      transform: translateX(20px);
      animation: toLeftGrow 1s ease 0.3s forwards;
    }
    .status {
      width: 150px;
      height: 0;
      border-width: 0 0 72px 72px;
      border-style: solid;
      border-color: transparent transparent #de1e30 transparent;
      margin-left: -60px;
      .text {
        height: 72px;
        line-height: 72px;
        font-size: $fs-16;
        color: $white;
        text-align: center;
      }
    }
  }
}
</style>
