<template>
  <div class="player">
    <v-header></v-header>
    <main class="w-1200">
      <v-bread-nav :breads="breads"></v-bread-nav>
      <div class="player-container mt-15">
        <div class="left-area">
          <v-desc :zhName="zhName" :enName="enName" :status="status" :imgUrl="imgUrl"></v-desc>
          <div class="info-box mt-15">
            <div class="info-detail br-rd--3">
              <div>
                <p>
                  <span>国籍：</span><span>{{ playerData.CountryName }}</span>
                </p>
                <p>
                  <span>出生日期：</span><span>{{ birthday }}</span>
                </p>
                <p>
                  <span>身高：</span><span>{{ playerData.BodyWeight }}cm</span>
                </p>
                <p>
                  <span>体重：</span><span>{{ playerData.Height }}kg</span>
                </p>
              </div>
              <div>
                <p>
                  <span>身价：</span><span>{{ playerData.Worth }}</span>
                </p>
                <p>
                  <span>位置：</span><span>{{ playerData.Position }}</span>
                </p>
                <p>
                  <span>所属球队：</span><span class="team">{{ playerData.ClubName }}</span>
                </p>
                <p>
                  <span>合同截止日期：</span><span>{{ playerData.Contract }}</span>
                </p>
              </div>
            </div>
            <div class="info-combat br-rd--3">
              <v-radar></v-radar>
            </div>
          </div>
          <div class="position-box mt-15  br-rd--3">
            <div class="position">
              <div class="graph-desc"></div>
              <div class="text-desc">
                <div class="posi">
                  <h3>主要位置</h3>
                  <p><i>-</i><span>暂无数据</span></p>
                </div>
                <div class="posi secondary">
                  <h3>二级位置</h3>
                  <div>
                    <p><i>-</i><span>暂无数据</span></p>
                    <p><i>-</i><span>暂无数据</span></p>
                  </div>
                </div>
              </div>
            </div>
            <div class="feature">
              <div class="merit">
                <div>优势</div>
                <div class="merit-details">
                  <p>稳定性</p>
                  <p>临门一脚</p>
                  <p>卸球</p>
                </div>
              </div>
              <div class="defect">
                <div>弱点</div>
                <div class="defect-details">
                  <p>暂未发现明显弱点</p>
                </div>
              </div>
            </div>
          </div>
          <div class="transfer-box br-rd--3">
            <div class="title"><h3>转会记录</h3></div>
            <table>
              <thead>
                <tr>
                  <th>转入时间</th>
                  <th>转会性质</th>
                  <th>转会费（单位：万欧元）</th>
                  <th>所效力球队</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item of transferList" :key="item.ID">
                  <td>{{ item.Time }}</td>
                  <td>转会</td>
                  <td>{{ item.Money }}</td>
                  <td>{{ item.Whereabouts }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="right-area">
          <v-intro type="球员简介">
            <div class="player-box">
              <div class="desc">
                暂无简介。。。
              </div>
              <div class="honor-wrap">
                <h3>球员历史上获得荣誉：</h3>
                <ul>
                  <li v-for="honor of honorList" :key="honor.ID">{{ honor.Msg }} {{ honor.Count }}</li>
                </ul>
              </div>
            </div>
          </v-intro>
          <div class="mt-15">
            <v-news-dyna :newsData="newsList"></v-news-dyna>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import VHeader from '@/components/v-header'
import VDesc from '@/components/v-desc'
import VRadar from '@/components/v-radar'
import VBreadNav from '@/components/v-bread-nav'
import VIntro from '@/components/v-intro'
import VNewsDyna from '@/components/v-news-dyna'
import { getPlayer } from '@/api/home'
export default {
  name: 'Player',
  components: {
    VHeader,
    VDesc,
    VRadar,
    VBreadNav,
    VIntro,
    VNewsDyna,
  },
  data() {
    return {
      breads: ['首页', '足球资料库'],
      zhName: '',
      enName: '',
      status: '',
      imgUrl: '',
      playerContent: '',
      newsList: [],
      honorList: [],
      playerData: {},
      birthday: '',
      transferList: [],
    }
  },
  mounted() {
    this.initPlayer()
  },
  methods: {
    async initPlayer() {
      const playerID = this.$route.params.id
      const res = await getPlayer({ PlayerID: playerID })
      const { AdvisoryList, PlayerAwardedLisrt, PlayerList, TransferList } = res.data.Data
      this.newsList = AdvisoryList
      this.honorList = PlayerAwardedLisrt
      this.playerData = PlayerList[0]
      this.transferList = TransferList
      this.birthday = this.playerData.DateBirth
      this.zhName = this.playerData.Name
      this.enName = this.playerData.EnglishName
      this.status = '身价：' + this.playerData.Worth
      this.imgUrl = this.playerData.Img
      this.breads.push(this.zhName)
    },
  },
}
</script>

<style lang="scss">
.player {
  .player-container {
    width: 100%;
    display: flex;
    .left-area {
      flex: 1;
    }
    .right-area {
      width: 300px;
      margin-left: 20px;
    }
    .info-box {
      display: flex;
      height: 230px;
    }
    .info-detail {
      flex: 1;
      background-color: $white;
      box-shadow: 0 2px 6px $br-color-base;
      display: flex;
      justify-content: space-between;
      padding: 15px 20px;
      box-sizing: border-box;
      font-size: $fs-12;
      p {
        padding: 8px 0;
        span:nth-child(2) {
          color: $h-color;
        }
        span.team {
          color: #de1e30;
        }
      }
    }
    .info-combat {
      flex: 1;
      margin-left: 15px;
      background-color: $white;
      box-shadow: 0 2px 6px $br-color-base;
      padding-top: 8px;
      box-sizing: border-box;
    }
    .position-box {
      padding: 15px;
      background-color: $white;
      display: flex;
      justify-content: space-between;
      box-shadow: 0 2px 6px $br-color-base;
    }
    .position {
      display: flex;
      .graph-desc {
        width: 267px;
        height: 170px;
        background: url(~@/assets/images/pic/court-bg.png) no-repeat;
      }
      .text-desc {
        color: $text-color-scd;
        margin-left: 25px;
        .posi {
          h3,
          p {
            padding: 3px 0;
          }
          i {
            font-size: $fs-16;
            color: #de1e30;
          }
          span {
            font-size: $fs-12;
            margin-left: 6px;
          }
        }
        .secondary {
          margin-top: 25px;
        }
      }
    }
    .feature {
      width: 310px;
      > div {
        height: 82px;
        display: flex;
        padding: 8px 15px;
        box-sizing: border-box;
      }
      .merit {
        background-color: #ecffe7;
        color: $text-color-scd;
      }
      .merit-details {
        margin-left: 20px;
        font-size: $fs-12;
        color: $success-color;
      }
      .defect {
        background-color: #f7f7f8;
        margin-top: 10px;
        color: $text-color-scd;
      }
      .defect-details {
        margin-left: 20px;
        font-size: $fs-12;
      }
      p {
        padding-bottom: 5px;
      }
    }
    .transfer-box {
      background-color: $white;
      // box-shadow: 0 2px 6px $br-color-base;
      margin-top: 30px;
      .title {
        padding: 12px 0;
        color: #de1e30;
        h3 {
          border-left: 4px solid #de1e30;
          padding-left: 12px;
        }
      }
      table {
        width: 100%;
        font-size: $fs-12;
        thead {
          background-color: $secondary-color;
          color: $text-color-scd;
        }
        th {
          height: 36px;
          vertical-align: middle;
        }
        td {
          height: 37px;
          vertical-align: middle;
          text-align: center;
          color: $h-color;
        }
      }
    }
    .player-box {
      padding: 10px;
      font-size: $fs-12;
      letter-spacing: 1.1px;
      color: $h-color;
      .desc {
        line-height: 18px;
      }
      .honor-wrap {
        margin-top: 30px;
        li {
          padding-top: 6px;
        }
      }
    }
  }
}
</style>
